<template>
    <div class="alert" :class="[modeClass,cssClass,{'alert-top':isAlertTop}]">
        <Backdrop @click.native="bdClick()" :enableBackdropDismiss="enableBackdropDismiss"
                  :isActive="isActive"></Backdrop>
        <transition name="alert"
                    @before-enter="beforeEnter"
                    @after-enter="afterEnter"
                    @before-leave="beforeLeave"
                    @after-leave="afterLeave">
            <div class="alert-wrapper" v-show="isActive">
                <div class="alert-head">
                    <h2 class="alert-title" v-if="title">{{title}}</h2>
                    <h3 class="alert-sub-title" v-if="subTitle" v-html="subTitle"></h3>
                </div>
                <div class="alert-message" v-html="message"></div>
                <div v-if="inputs && inputs.length>0">
                    <div v-if="inputType==='radio'" class="alert-radio-group" role="radiogroup">
                        <Button role="alert-radio-button" v-for="(i,index) in inputsForDispaly" @click="rbClick(i)"
                                :key="index"
                                :aria-checked="i.checked" :disabled="i.disabled" :id="i.id"
                                class="alert-tappable alert-radio">
                            <div class="alert-radio-icon">
                                <div class="alert-radio-inner"></div>
                            </div>
                            <div class="alert-radio-label">
                                <span>{{i.label}}</span>
                            </div>
                        </Button>
                    </div>

                    <div class="alert-checkbox-group" v-if="inputType==='checkbox'">
                        <Button :id="i.id" role="alert-checkbox-button" :aria-checked="i.checked"
                                v-for="(i,index) in inputsForDispaly" @click="cbClick(i)" :key="index"
                                :checked="i.checked" :disabled="i.disabled" class="alert-tappable alert-checkbox">
                            <div class="alert-checkbox-icon">
                                <div class="alert-checkbox-inner"></div>
                            </div>
                            <div class="alert-checkbox-label">
                                <span>{{i.label}}</span>
                            </div>
                        </Button>
                    </div>

                    <div v-if="inputType!='radio' && inputType!='checkbox'" class="alert-input-group">
                        <div v-for="i in inputsForDispaly" class="alert-input-wrapper">
                            <input :id="i.id" :value="i.value" :placeholder="i.placeholder" :type="i.type"
                                   class="alert-input">
                        </div>
                    </div>

                </div>
                <div class="alert-button-group" :class="{'alert-button-group-vertical':buttonsForDisplay.length>2}">
                    <Button role="alert-button" v-for="(b,index) in buttonsForDisplay" :key="index" @click="btnClick(b)"
                            :class="[b.cssClass]">
                        <span>{{b.text}}</span>
                    </Button>
                </div>
            </div>
        </transition>
    </div>
</template>
<style lang="scss">
    @import './alert.scss';
    @import './alert.ios.scss';
    @import './alert.md.scss';

    // transitioName = 'alert'
    .alert-enter-active, .alert-leave-active {
        transform: scale(1);
        opacity: 1;
    }

    .alert-enter {
        transform: scale(1.1);
        opacity: 0;
    }

    .alert-leave-active {
        transform: scale(0.9);
        opacity: 0;
    }

</style>
<script type="text/javascript">
  /**
   * @component Alert
   * @description
   *
   * ##  / Alert
   *
   * , ,  : Alert/Confirm/Radios/Checkboxes/Input.
   *
   * , SelectRadio/Checkboxes, . , Input, , urlemailtext, modal.
   *
   *
   * ### buttons
   * - role: cancelios, cancelbutton, cancelhandler
   * - cssClass: button, , cssClass
   * - handler: , `this.$alert.dismiss()`Promise, 
   *
   * ### inputs
   * - inputstype, : input/checkbox/radio
   * - , .
   * - inputtype: text/tel/number/email -> : type/name/placeholder/value
   * - inputtype: checkbox/radio -> : type/value/label/checked/disabled
   * - , 
   *
   *
   * ### 
   *
   * - , /
   * - , , `dismissOnPageChange`
   * - button
   * - demo
   *
   *
   * @props {String} title              - Alert Instance
   * @props {string} [subTitle]         - Alert Instance
   * @props {string} [message]          - Alert Instance
   * @props {string} [cssClass]         - Additional classes for custom styles, separated by spaces
   * @props {Array} [buttons]           - buttonrole
   * @props {string} buttons.text           - button
   * @props {string} buttons.handler        - button, 
   * @props {string} buttons.role           - button, :cancel/null
   * @props {string} buttons.cssClass       - buttonclass
   *
   * @props {Array} [inputs]            - alertinputform
   * @props {Array} inputs.type             - input, : text, tel, number, etc.
   * @props {Array} inputs.name             - 
   * @props {Array} inputs.placeholder      - 
   * @props {Array} inputs.value            - 
   * @props {Array} inputs.label            - 
   * @props {Array} inputs.checked          - 
   * @props {Array} inputs.id               - id
   *
   * @props {Boolean} [enableBackdropDismiss=true]  - backdropactionsheet
   * @props {Boolean} [dismissOnPageChange=true]    - 
   * @props {String} [mode=ios]                     - 
   *
   *
   * @demo https://dtfe.github.io/vimo-demo/#/alert
   * @usage
   * // Alert
   * this.$alert.present({
   *  title: 'Alert',
   *  message: 'alert',
   *  cssClass: 'alertCssOuterMain  ',
   *  enableBackdropDismiss: true,
   *  buttons: [
   *    {
   *      text: '',
   *      handler: (value) => {
   *        this.$alert.dismiss().then(function (data) {
   *          console.debug('button3 click dismiss ')
   *          console.debug(data)
   *        });
   *      }
   *    }
   *  ]
   * });
   *
   * // Input
   * input () {
   *    this.$alert.present({
   *      title: 'iTunes Store',
   *      // subTitle: 'alert',
   *      message: 'Apple ID"apple@icloud.com"',
   *      cssClass: 'alertCssOuterMain  ',
   *      enableBackdropDismiss: true,
   *      inputs: [
   *        {
   *          type: 'password',
   *          name: 'password',
   *          placeholder: '',
   *          value: '',
   *        },
   *      ],
   *      buttons: [
   *        {
   *          text: '',
   *          role: 'cancel',
   *          handler: () => {}
   *        },
   *        {
   *          text: '',
   *          role: '',
   *          handler: (value) => {
   *            this.$alert.present({
   *              title: '',
   *              // subTitle: 'alert',
   *              message: '' + JSON.stringify(value),
   *              cssClass: '',
   *              enableBackdropDismiss: true,
   *              buttons: [
   *                {
   *                  text: '',
   *                  role: 'cancel',
   *                  handler: (value) => {
   *                    this.$alert.dismiss().then(function (msg) {
   *                      console.log('alert input2 dismiss promise')
   *                    })
   *                  }
   *                }
   *              ]
   *            }).then(function () {
   *              console.log('alert input2 present promise')
   *            })
   *          }
   *        }
   *      ]
   *    }).then(function () {
   *      console.log('alert input1 present promise')
   *    });
   *  },
   *
   */
  import { Backdrop } from '../backdrop'
  import { Button } from '../button'
  import { urlChange } from '../../util/util'
  export default{
    name: 'Alert',
    props: {
      title: [String],
      subTitle: [String],
      cssClass: [String],
      message: [String],
      // Array buttonrole
      buttons: {
        type: Array,
        default () { return [] }
      },
      // alertinputform
      // input -> text/tel/number/email ....
      // type/name/placeholder/value

      // input ->  checkbox/radio
      // type/value/label/checked/disabled
      inputs: {
        type: Array,
        default () { return [] }
      },
      // Boolean backdropactionsheet
      enableBackdropDismiss: {
        type: Boolean,
        default () { return true }
      },
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode', 'ios') || 'ios' }
      },
      dismissOnPageChange: {
        type: Boolean,
        default () { return true }
      }
    },
    data () {
      return {

        /**
         * Alert State
         * @private
         * */
        inputsForDispaly: [],   // inputs
        isActive: false,        // 
        enabled: false,         // false

        inputType: null,        // Alertinputradiocheckbox
        isAlertTop: false,      // alertinput

        dismissCallback: null,
        presentCallback: null,
        unreg: null             // url
      }
    },
    computed: {
      // Alert
      modeClass () {
        return `alert-${this.mode}`
      },
      buttonsForDisplay () {
        // string
        return this.buttons.map(button => {
          if (typeof button === 'string') {
            return {text: button}
          }
          return button
        })
      }
    },
    methods: {
      // -------- private --------

      /**
       * ActionSheet Animate Hooks
       * @private
       * */
      beforeEnter () {
        this.$app && this.$app.setEnabled(false, 200)
        this.enabled = false // 
      },
      afterEnter () {
        // promise
        this.presentCallback()

        this.focusOutActiveElement()
        let focusableEle = this.$el.querySelector('input')
        if (focusableEle) {
          focusableEle.focus()
        }
        this.enabled = true
      },
      beforeLeave () {
        this.$app && this.$app.setEnabled(false, 200)
        this.enabled = false
      },
      afterLeave () {
        // promise
        this.dismissCallback()
        // DOM
        this.$el.remove()
        this.enabled = true
      },

      /**
       * @function bdClick
       * @description
       * backdrop,actionsheet
       * cancelactionsheet
       * @private
       * */
      bdClick () {
        if (this.enabled && this.enableBackdropDismiss && this.buttonsForDisplay.length > 0) {
          let cancelBtn = this.buttonsForDisplay.find(b => b.role === 'cancel')
          if (cancelBtn) {
            this.btnClick(cancelBtn)
          } else {
            this.dismiss()
          }
        }
      },

      /**
       * @function btnClick
       * @description
       * 
       * @param {object} button  - buttonrole
       * @private
       * */
      btnClick (button) {
        if (!this.enabled) {
          return
        }

        let shouldDismiss = true

        if (button.handler) {
          // a handler has been provided, execute it
          // pass the handler the values from the inputs
          if (button.handler(this.getValues()) === false) {
            // if the return value of the handler is false then do not dismiss
            shouldDismiss = false
          }
        }

        if (shouldDismiss) {
          this.dismiss()
        }
      },

      /**
       * @function rbClick
       * @description
       * Radio Button Click
       * @param {object} checkedInput  - Radio 
       * @private
       * */
      rbClick (checkedInput) {
        if (this.enabled) {
          this.inputsForDispaly.forEach(input => {
            input.checked = (checkedInput === input)
          })
          if (checkedInput.handler) {
            checkedInput.handler(checkedInput)
          }
        }
      },

      /**
       * @function cbClick
       * @description
       * CheckBox Button Click
       * @param {object} checkedInput  - CheckBox 
       * @private
       * */
      cbClick (checkedInput) {
        if (this.enabled) {
          checkedInput.checked = !checkedInput.checked
          if (checkedInput.handler) {
            // 
            checkedInput.handler(JSON.parse(JSON.stringify(checkedInput)))
          }
        }
      },

      /**
       * inputs
       * @private
       * */
      getValues () {
        if (this.inputType === 'radio' && this.inputsForDispaly.length > 0) {
          // this is an alert with radio buttons (single value select)
          // return the one value which is checked, otherwise undefined
          const checkedInput = this.inputsForDispaly.find(i => i.checked)
          return checkedInput ? checkedInput.value : undefined
        }

        if (this.inputType === 'checkbox' && this.inputsForDispaly.length > 0) {
          // this is an alert with checkboxes (multiple value select)
          // return an array of all the checked values
          return this.inputsForDispaly.filter(i => i.checked).map(i => i.value.trim())
        }

        // this is an alert with text inputs
        // return an object of all the values with the input name as the key
        // inputv-modelid
        const values = {}
        this.inputsForDispaly.forEach(i => {
          let _$id = document.getElementById(i.id)
          if (_$id && _$id.value) {
            values[i.name] = _$id.value.trim()
          } else {
            values[i.name] = null
          }
        })
        return values
      },

      /**
       * ActionSheetfocus
       * @private
       * */
      focusOutActiveElement () {
        // only button
        const activeElement = document.activeElement
        activeElement && activeElement.blur && activeElement.blur()
      },

      // -------- public --------

      /**
       * @function present
       * @description
       * 
       * @param {Object} options - props, alert.js
       * @returns {Promise} resolved
       */
      present () {
        this.isActive = true
        return new Promise((resolve) => { this.presentCallback = resolve })
      },

      /**
       * @function dismiss
       * @description
       * 
       * @returns {Promise} resolved
       */
      dismiss () {
        if (this.isActive) {
          this.isActive = false // 
          this.unreg && this.unreg()
          if (!this.enabled) {
            this.$nextTick(() => {
              this.dismissCallback()
              this.$el.remove()
              this.enabled = true
            })
          }
          return new Promise((resolve) => { this.dismissCallback = resolve })
        } else {
          return new Promise((resolve) => { resolve() })
        }
      },

      /**
       * inputs
       * @private
       * */
      init () {
        if (!this.inputs || this.inputs.length === 0) {
          return []
        }
        // 
        let _inputs = []
        _inputs = this.inputs.map((input, index) => {
          return {
            type: input.type || 'text',
            name: (input.name) ? input.name : index,
            placeholder: (input.placeholder) ? input.placeholder : '',
            value: (input.value) ? input.value : '',
            label: input.label,
            checked: input.checked,
            disabled: input.disabled,
            id: `alert-input-${index}`, // used for input -> text/tel/number/password
            handler: (input.handler) ? input.handler : null
          }
        })

        let inputTypes = []
        _inputs.forEach(input => {
          if (inputTypes.indexOf(input.type) < 0) {
            inputTypes.push(input.type)
          }
        })
        if (inputTypes.length > 1 && (inputTypes.indexOf('checkbox') > -1 || inputTypes.indexOf('radio') > -1)) {
          console.warn(`Alert cannot mix input types: ${(inputTypes.join('/'))}. Please see alert docs for more info.`)
          console.warn(`Alert input: ${(inputTypes.join('/'))}. .`)
        }

        this.inputType = inputTypes.length ? inputTypes[0] : null

        // const checkedInput = _inputs.find(input => input.checked)

        const NON_TEXT_INPUT_REGEX = /^(radio|checkbox|range|file|submit|reset|color|image|button)$/i

        const hasTextInput = (_inputs.length && _inputs.some(i => !(NON_TEXT_INPUT_REGEX.test(i.type))))
        // if (hasTextInput && this._platform.is('mobile')) {
        if (hasTextInput) {
          // this alert has a text input and it's on a mobile device so we should align
          // the alert up high because we need to leave space for the virtual keboard
          // this also helps prevent the layout getting all messed up from
          // the browser trying to scroll the input into a safe area
          this.isAlertTop = true
        }

        this.inputsForDispaly = _inputs
      }
    },
    created () {
      this.init()
      // mounted before data ready, so no need to judge the `dismissOnPageChange` value
      if (this.dismissOnPageChange) {
        this.unreg = urlChange(() => {
          this.isActive && this.dismiss()
        })
      }
    },
    components: {
      'Backdrop': Backdrop,
      'Button': Button
    }
  }
</script>



