<template>
    <div class="ion-item-options" :side="side">
        <slot></slot>
    </div>
</template>
<script type="text/javascript">
  /**
   * @component ItemOptions
   * @description
   *
   * ItemSliding, .
   *
   * @props {String} [side='right'] - 
   *
   * @slot  - , 
   *
   *
   * @demo https://dtfe.github.io/vimo-demo/#/slidingList
   * @see component:ItemSliding
   * */
  export default{
    name: 'ItemOptions',
    data () {
      return {}
    },
    props: {
      // item
      side: {
        type: String,
        default: 'right'
      }
    },
    methods: {
      /**
       * options
       * @private
       * */
      width () {
        // let _display = this.$el.display;
        // this.$el.display = 'block';
        let _offsetWidth = this.$el.offsetWidth
        // this.$el.display = _display;
        return _offsetWidth
      }
    }
  }
</script>



