<template>
    <Page>
        <Header>
            <Navbar ref="navbar" color="primary">
                <Title ref="title" @onTitleClick="onTitleClickHandler">API Test</Title>
                <Buttons right slot="buttons">
                    <Button color="dark" @click="personHandler" role="bar-button">
                        <Icon name="icon-vue"></Icon>
                    </Button>
                    <Button @click="showPopMenu" role="bar-button">
                        <Icon name="icon-more"></Icon>
                    </Button>
                </Buttons>
            </Navbar>
        </Header>
        <Content padding class="outer-content">
            <h5>
                {{platformName}}
            </h5>
            <p>Vimo <strong>JSSDK</strong>, , <strong>H5</strong>, AlipayDingtalk, . , ...
            </p>

            <h5 text-danger class="text-danger"></h5>
            <Button block @click="openToast">Toast</Button>
            <Button block @click="openActionSheet">ActionSheet</Button>
            <Button block @click="openAlert">Alert</Button>
            <Button block @click="openConfirm">Confirm</Button>
            <Button block @click="openPrompt">Prompt</Button>
            <Button block @click="openLoading">Loading</Button>

            <h5></h5>
            <Button block @click="btnTitle"></Button>
            <Button block @click="btnImage">(url)</Button>
            <Button block @click="showOptionButton"></Button>
            <Button block @click="hideOptionButton"></Button>
            <Button block @click="btnReset"></Button>

            <h5></h5>
            <Button block @click="showPopMenu"></Button>
            <h5>Picker</h5>

            <p>: {{smoothie}}</p>
            <Button block @click="twoColumns">Picker</Button>

            <h5></h5>
            <Button block @click="openAlbum"></Button>

        </Content>
    </Page>
</template>
<style scoped lang="scss">
    .icon-vue {
        height: 30px;
        width: 30px;
        box-sizing: content-box;
        background: url('http://cn.vuejs.org/images/logo.png') no-repeat left center/30px 30px;
    }

    .icon-more {
        height: 30px;
        width: 30px;
        box-sizing: content-box;
        background: url('https://avatars0.githubusercontent.com/u/26055200?v=3&s=200') no-repeat left center/30px 30px;
    }

    .icon-vimo {
        height: 30px;
        width: 30px;
        background: url('../assets/img/vimo.png') no-repeat center center/30px 30px;
    }

    .icon-alipay {
        height: 30px;
        width: 40px;
        background: url('../assets/img/alipay.png') no-repeat center center/cover;
    }

    .icon-wechat {
        height: 30px;
        width: 36px;
        background: url('../assets/img/wechat.png') no-repeat center center/cover;
    }
</style>
<script type="text/javascript">
  import { Badge } from 'vimo/components/badge'
  import { Picker } from 'vimo/components/picker'
  import { PreviewImage } from 'vimo/components/preview-image'
  export default{
    name: 'name',
    data () {
      return {
        platformName: 'H5',
        smoothie: `Banana Watermelon`
      }
    },
    props: {},
    watch: {},
    computed: {
      titleComponent () {
        return this.$refs.title
      },
      navbarComponent () {
        return this.$refs.navbar
      }
    },
    methods: {
      personHandler () {
        this.$alert.present({
          title: '',
          message: ', !',
          buttons: [
            {
              text: '',
              handler: () => {
                console.log(' clicked')
              }
            }
          ]
        })
      },
      /**
       * 
       * */
      openToast () {
        this.$toast({
          type: 'success',
          message: '',
          duration: 3000,
          onDismiss () {
            console.debug('Toast ')
          }
        })
      },
      openActionSheet () {
        this.$actionSheet.present({
          title: '',
          buttons: [
            {
              text: '',
              role: 'destructive',
              handler: () => {
                console.log(' clicked')
                this.$actionSheet.present({
                  title: '',
                  buttons: [
                    {
                      text: '',
                      handler: () => {
                        console.log(' clicked')
                      }
                    },
                    {
                      text: '',
                      handler: () => {
                        console.log(' clicked')
                      }
                    },
                    {
                      text: '',
                      handler: () => {
                        console.log(' clicked')
                      }
                    },
                    {
                      text: '',
                      role: 'cancel',
                      handler: () => {
                        console.debug(' clicked')
                      }
                    }
                  ]
                })
              }
            },
            {
              text: '',
              handler: () => {
                console.log(' clicked')
              }
            },
            {
              text: '',
              handler: () => {
                console.log(' clicked')
              }
            },
            {
              text: '',
              role: 'cancel',
              handler: () => {
                console.log(' clicked')
              }
            }
          ]
        }).then(function () {
          console.log('actionsheet  promise')
        })
      },
      openAlert () {
        this.$alert.present({
          title: '',
          message: '',
          buttons: [
            {
              text: '',
              handler: () => {
                console.log(' clicked')
              }
            }
          ]
        })
      },
      openConfirm () {
        this.$alert.present({
          title: '',
          message: '\n1234567890',
          buttons: [
            {
              text: '',
              role: 'cancel',
              handler: () => {
                console.log(' click')
              }
            },
            {
              text: '',
              handler: () => {
                console.log(' click')
              }
            }
          ]
        })
      },
      openPrompt () {
        this.$alert.present({
          title: 'iTunes Store',
          message: 'Apple ID"apple@icloud.com"',
          enableBackdropDismiss: true,
          inputs: [
            {
              type: 'password',
              name: 'password',
              placeholder: '',
              value: ''
            }
          ],
          buttons: [
            {
              text: '',
              role: 'cancel',
              handler: () => {}
            },
            {
              text: '',
              handler: (value) => {
                this.$alert.present({
                  title: '',
                  message: '' + JSON.stringify(value),
                  cssClass: '',
                  enableBackdropDismiss: true,
                  buttons: [
                    {
                      text: '',
                      role: 'cancel',
                      handler: (value) => {
                        this.$alert.dismiss().then(function () {
                          console.log('Alert  click')
                        })
                      }
                    }
                  ]
                }).then(function () {
                  console.log('Alert present promise')
                })
              }
            }
          ]
        }).then(function () {
          console.log('Input present!')
        })
      },
      openLoading () {
        this.$loading.present({
          content: ''
        })

        window.setTimeout(() => {
          this.$loading.dismiss('loading.dismiss')
        }, 3000)
      },

      /**
       * 
       * */
      btnTitle () {
        this.titleComponent.setTitle('Hello Vimo', true)
      },
      btnImage () {
        this.titleComponent.setTitle({
          image: 'https://zos.alipayobjects.com/rmsportal/jFCQxeOXeKTQUfhrQFOo.png'
        })
      },
      btnReset () {
        this.titleComponent.setTitle('', true)
        this.navbarComponent.reset()
        this.titleComponent.reset()
      },
      showPopMenu () {
        let data = [
          {
            title: '',
            icon: 'https://zos.alipayobjects.com/rmsportal/mzorSIxVEdkTuxumzzau.png',
            badge: '-1',
            handler () {
              console.log('index:0 : ')
            }
          },
          {
            title: '',
            icon: 'https://zos.alipayobjects.com/rmsportal/UoBNIZJosEXNQtAxCEUg.png',
            badge: '100',
            handler () {
              console.log('index:1 : ')
            }
          },
          {
            title: '',
            icon: 'https://zos.alipayobjects.com/rmsportal/QJeWMNUFFiDCQawMLPTr.png',
            badge: '12',
            handler () {
              console.log('index:2 : ')
            }
          },
          {
            title: '',
            icon: 'data:image/png;base64,',
            badge: '0',
            handler () {
              console.log('index:3 : ')
            }
          }
        ]
        this.navbarComponent.showPopMenu(data)
      },
      showOptionButton () {
        this.navbarComponent.showOptionButton()
      },
      hideOptionButton() {
        this.navbarComponent.hideOptionButton()
      },
      onTitleClickHandler () {
        alert(' platform-api.vue ')
      },
      twoColumns () {
        let arr = this.smoothie.split(' ')

        let data = {
          buttons: [
            {
              text: '',
              role: 'cancel'
            },
            {
              text: '',
              handler: (data) => {
                this.smoothie = `${data.flavor1.value} ${data.flavor2.value}`
                console.log(data)
                console.log(data)
                console.log(JSON.stringify(data))
              }
            }
          ],
          columns: [
            {
              name: 'flavor1',
              align: 'right',
              selectedIndex: 1,
              options: [
                {text: 'Mango'},
                {text: 'Banana'},
                {text: 'Cherry'},
                {text: 'Strawberry'},
                {text: 'Raspberry'},
                {text: 'Blueberry'},
                {text: 'Peach'},
                {text: 'Coconut'},
                {text: 'Pineapple'},
                {text: 'Honeydew'},
                {text: 'Watermelon'},
                {text: 'Grape'},
                {text: 'Avocado'},
                {text: 'Kiwi'},
                {text: 'Orange'},
                {text: 'Papaya'}
              ]
            },
            {
              name: 'flavor2',
              align: 'left',
              selectedIndex: 3,
              options: [
                {text: 'Banana'},
                {text: 'Orange'},
                {text: 'Grape'},
                {text: 'Watermelon'},
                {text: 'Strawberry'},
                {text: 'Papaya'},
                {text: 'Kiwi'},
                {text: 'Cherry'},
                {text: 'Raspberry'},
                {text: 'Mango'},
                {text: 'Pineapple'},
                {text: 'Peach'},
                {text: 'Avocado'},
                {text: 'Honeydew'},
                {text: 'Blueberry'},
                {text: 'Coconut'}
              ]
            }
          ]
        }

        arr.forEach((item, index) => {
          let options = data.columns[index].options
          for (let i = 0, len = options.length; len > i; i++) {
            if (options[i].text === item) {
              data.columns[index].selectedIndex = i
            }
          }
        })

        Picker.present(data)
      },
      openAlbum () {
        PreviewImage({
          current: 1,
          urls: [
            'https://img.alicdn.com/tps/TB1sXGYIFXXXXc5XpXXXXXXXXXX.jpg',
            'https://img.alicdn.com/tps/TB1pfG4IFXXXXc6XXXXXXXXXXXX.jpg',
            'https://img.alicdn.com/tps/TB1h9xxIFXXXXbKXXXXXXXXXXXX.jpg'
          ]
        })
      }
    },
    mounted () {
      let platforms = this.$platform.platforms()
      if (platforms.length === 3) {
        this.platformName = platforms[2].toUpperCase()
      }
    },
    activated () {},
    deactivate () {},
    components: {Badge},
    destroyed () {}
  }
</script>



