/**
 * @component PreviewImage
 * @description
 *
 * ##  / PreviewImage
 *
 * ### 
 *
 * 
 *
 * ### 
 *
 * 
 *
 * @params {Number} [current=0]     - index
 * @params {Array} [urls]           - 
 * @params {Array} [isH5=false]     - h5, false, trueh5(isForceH5)
 *
 * */
import { Modal } from '../modal'
import PreviewImageComponent from './preview-image.vue'
export function PreviewImage (options) {
  let isAlipayReady = window.VM.platform.is('alipay') && window.AlipayJSBridge && !options.isH5
  // alipayurl, base64
  if (isAlipayReady) {
    // alipay
    console.info('PreviewImage Alipay!')
    window.ap.previewImage({
      current: options.current || 0,
      urls: options.urls
    })
  } else {
    console.info('PreviewImage H5!')
    Modal.present({
      component: PreviewImageComponent,
      mode: 'zoom',
      data: {
        current: options.current || 0,
        urls: options.urls
      },
      showBackdrop: true,
      enableBackdropDismiss: true
    })
  }
}


