<template>
    <div class="ion-loading" :class="[modeClass,cssClass]">
        <Backdrop :isActive="isActive && showBackdrop" :enableBackdropDismiss="false"></Backdrop>
        <transition :name="transitionClass"
                    @before-enter="beforeEnter"
                    @after-enter="afterEnter"
                    @before-leave="beforeLeave"
                    @after-leave="afterLeave">
            <div class="loading-wrapper" v-show="isActive">
                <div v-if="showSpinner" class="loading-spinner">
                    <Spinner :name="spinner"></Spinner>
                </div>
                <div v-if="content" v-html="content" class="loading-content"></div>
            </div>
        </transition>
    </div>
</template>
<style lang="scss">
    @import './loading.scss';
    @import './loading.ios.scss';
    @import './loading.md.scss';

    .indicator {
        .loading-wrapper {
            background: rgba(0, 0, 0, 0.9);
            color: #fff;
            padding: 13px 13px;
            circle, line {
                stroke: #fff !important;
            }
            .loading-spinner {
                height: 30px;
                width: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }

    .indicator.reverse {
        .loading-wrapper {
            background: rgba(256, 256, 256, 0.9);
            circle, line {
                stroke: #000 !important;
            }
        }
    }
</style>
<script type="text/javascript">
  /**
   * @component Loading
   * @description
   *
   * ##  / Loading(Loading)
   *
   * Loading
   *
   * ### Loading
   *
   * Loading: //, Loading, Loading"**, !**", , , Loading, Indicator, Loading, , , .
   *
   * ### 
   *
   * - Loadingpresent, duration, dismiss,
   * - Vimo, `body`, App. .
   * - , 
   *
   * ### 
   *
   * - 
   * - stringstring
   * - 
   *
   *
   * @props {string} [spinner='ios'] - 
   * @props {string} [content] - 
   * @props {string} [cssClass] - 
   * @props {boolean} [showBackdrop=false] - 
   * @props {number} [duration=5000] - loading, 0, 5000ms
   * @props {boolean} [dismissOnPageChange=true] - loading
   * @props {string} [mode='ios'] - 
   *
   * @demo https://dtfe.github.io/vimo-demo/#/loading
   * @see component:Indicator
   * @usage
   *
   * // 
   * spinnerOnly () {
   *      this.$loading.present();
   *      setTimeout(() => {
   *        this.$loading.dismiss().then(() => {
   *          console.debug('dismiss in promise success!')
   *        })
   *      }, 1000)
   *    },
   *
   * // name
   * stringOnly () {
   *      this.$loading.present('String');
   *      setTimeout(() => {
   *        this.$loading.dismiss().then(() => {
   *          console.debug('dismiss in promise success!')
   *        })
   *      }, 1000)
   *    },
   *
   * // 
   * showDefault () {
   *      this.$loading.present({
   *        content: ', 6000ms...',
   *        dismissOnPageChange: false, // urlloading()
   *        showBackdrop: true,
   *      });
   *      setTimeout(() => {
   *        this.$loading.dismiss().then(() => {
   *          console.debug('dismiss in promise success!')
   *        })
   *      }, 6000);
   *    },
   *
   * */
  import { urlChange } from '../../util/util'
  import { Backdrop } from '../backdrop'
  import { Spinner } from '../spinner'
  export default{
    name: 'Loading',
    props: {
      spinner: {
        type: String,
        default () { return this.$config && this.$config.get('spinner', 'ios') || 'ios' }
      },
      content: [String],
      cssClass: [String],
      showBackdrop: {
        type: Boolean,
        default () { return true }
      },
      duration: Number,
      dismissOnPageChange: {
        type: Boolean,
        default () { return true }
      },
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      }
    },
    data () {
      return {
        isActive: false, // 
        enabled: false, // (  )

        // promise
        presentCallback: null,
        dismissCallback: null,

        startTime: null,
        timer: null,
        unreg: null
      }
    },
    computed: {
      // ActionSheet
      modeClass () {
        return `loading-${this.mode}`
      },
      showSpinner () {
        return this.spinner !== 'hide'
      },
      transitionClass () {
        return `loading-${this.mode}`
      }
    },
    methods: {
      // -------- private --------
      /**
       * ActionSheet Animate Hooks
       * */
      beforeEnter () {
        this.$app && this.$app.setEnabled(false, 200)
        this.enabled = false
      },
      afterEnter () {
        this.presentCallback()
        this.enabled = true
      },
      beforeLeave () {
        this.$app && this.$app.setEnabled(false, 200)
        this.enabled = false
      },
      afterLeave () {
        // DOM
        this.dismissCallback()
        this.$el.remove()
        this.enabled = true
      },

      // -------- public --------
      /**
       * @function present
       * @description
       * Loading
       * @param {Object} options - props, loading.js
       * @returns {Promise}  Promise, resolved
       */
      present () {
        this.startTime = new Date().getTime()
        this.isActive = true
        if (parseInt(this.duration) > 16) {
          this.timer && window.clearTimeout(this.timer)
          this.timer = window.setTimeout(() => {
            this.dismiss()
          }, this.duration)
        }
        return new Promise((resolve) => { this.presentCallback = resolve })
      },

      /**
       * @function dismiss
       * @description
       * Loading
       * @return {Promise} Promise, resolved
       * */
      dismiss () {
        if (this.isActive) {
          this.isActive = false // 
          this.timer && window.clearTimeout(this.timer)
          this.unreg && this.unreg()
          if (!this.enabled) {
            this.$nextTick(() => {
              this.$el.remove()
              this.dismissCallback()
              this.enabled = true
            })
          }
          return new Promise((resolve) => { this.dismissCallback = resolve })
        } else {
          return new Promise((resolve) => { resolve() })
        }
      }
    },
    created () {
      if (this.dismissOnPageChange) {
        this.unreg = urlChange(() => {
          this.isActive && this.dismiss()
        })
      }
    },
    components: {
      Backdrop, Spinner
    }
  }
</script>



