/**
 * Created by Hsiang on 2017/5/8.
 *
 *
 * @property {object} options - options
 *
 * ## 
 *
 * H5, H5api.
 *
 * , , hybrid, .
 *
 * , , .
 *
 *
 * ## 
 *
 * #### getCurrentPosition()
 *
 * , h5, : h5/qMap/bMap/aMap, . promise
 *
 * #### watchPosition()
 *
 * , , . getCurrentPosition(). id
 *
 * ```
 * this.getGeolocationByH5().then(() => {
 *    this.h5Timer = this.$geo.watchPosition((position) => {
 *       this.h5Geo = position
 *    })
 * })
 *
 * ```
 *
 * #### clearWatch(id)
 *
 * 
 *
 */
const VERSION = '0.1.0'
const isString = (val) => typeof val === 'string'
const isFunction = (val) => typeof val === 'function'
const H5 = 'h5'
const ALIPAY = 'alipay'
const Q_MAP = 'qMap' // 
const B_MAP = 'bMap' // 
const A_MAP = 'aMap' // ()
const ERROR_TYPE = {
  1: 'PERMISSION_DENIED',
  2: 'POSITION_UNAVAILABLE',
  3: 'TIMEOUT'
}

class Geolocation {
  /**
   * @param {Object} options - , Map
   * */
  constructor (options) {
    this._version = VERSION
    this._ua = window.navigator.userAgent.toLowerCase()
    // setting
    this._s = {}
    // default
    this._d = {
      enableHighAccuracy: true, // 
      maximumAge: 1000,         // 1s1s
      timeout: 4000,            // 5s
      fallBack: 'aMap',         // , IOSHTTP, , aMap
      [Q_MAP]: {
        key: 'OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77', // official example app key, please use geo.register() to replace
        name: 'qqMapName'
      },
      [B_MAP]: {
        key: ''
      },
      [A_MAP]: {
        key: ''
      }
    }

    this._aMapGeolocation = null // aMap geolocation
    this._bMapGeolocation = null // bMap geolocation
    this._qMapGeolocation = null // qMap geolocation

    this.isHttps = window.location.protocol === 'https:'
    this.isSupported = !!window.navigator.geolocation
    this.isIos = this._ua.indexOf('iphone') > -1 || this._ua.indexOf('ipad') > -1

    // 
    Object.assign(this._s, this._d, options)
  }

  /**
   * 
   * , , H5
   * @param {string} [type] - map
   * @return {Promise}
   * */
  getCurrentPosition (type) {
    /**
     * , .
     * 
     * */
    let isAlipayReady = this._ua.indexOf('alipay') > -1 && window.AlipayJSBridge
    if (!type) {
      if (isAlipayReady) {
        type = ALIPAY
      } else {
        type = H5
      }
    }

    if (type === H5 && this.isIos && !this.isHttps) {
      type = this._s.fallBack
      console.info(`[GEO]:  getCurrentPosition() -> IOSHTTPS, '${type}'!`)
    }

    return new Promise((resolve, reject) => {
      /**
       * 
       * */
      const successFn = (pos) => {
        let position = {}
        switch (type) {
          case H5:
            position = {lat: pos.coords.latitude, lng: pos.coords.longitude, mapType: H5, full: pos}
            break
          case A_MAP:
            position = {lat: pos.position.lat, lng: pos.position.lng, mapType: A_MAP, full: pos}
            break
          case B_MAP:
            position = {lat: pos.point.lat, lng: pos.point.lng, mapType: B_MAP, full: pos}
            break
          case Q_MAP:
            position = {lat: pos.lat, lng: pos.lng, mapType: Q_MAP, full: pos}
            break
          case ALIPAY:
            position = {lat: pos.latitude, lng: pos.longitude, mapType: ALIPAY, full: pos}
            break
          default:
            position = {lat: pos.position.lat, lng: pos.position.lng, mapType: A_MAP, full: pos}
            break
        }
        resolve(position)
      }

      /**
       * 
       * */
      const errorFn = (err) => {
        let errType = Object.prototype.toString.call(err).match(/^(\[object )(\w+)\]$/i)[2].toLowerCase()
        if (errType === 'event') {
          console.warn(`[GEO]:  ${err.target.src}`)
          reject(err)
          return
        }

        if (errType === 'string') {
          console.warn(`[GEO]: ${err}`)
          reject(err)
          return
        }

        switch (type) {
          case H5:
            // alert(JSON.stringify(err))
            err = `[GEO]: CODE->${err.code}; INFO->${ERROR_TYPE[err.code]}; MESSAGE->${err.message}`
            console.warn(err)
            break
          case A_MAP:
            err = `[GEO]: INFO->${err.info}; MESSAGE->${err.message}`
            console.warn(err)
            break
          case B_MAP:
            err = `[GEO]: ${JSON.stringify(err)}`
            console.warn(err)
            break
          case Q_MAP:
            err = `[GEO]: ${JSON.stringify(err)}`
            console.warn(err)
            break
          default:
            err = `[GEO]: INFO->${err.info}; MESSAGE->${err.message}`
            console.warn(err)
            break
        }
        reject(err)
      }

      /**
       * map
       * */
      const posOptions = {
        enableHighAccuracy: this._s.enableHighAccuracy,
        maximumAge: this._s.maximumAge,
        timeout: this._s.timeout
      }

      if (type === H5) {
        if (this.isSupported) {
          this._h5Location(successFn, errorFn, posOptions)
        } else {
          console.info(`[GEO]:'window.navigator.geolocation', '${this._s.fallBack}'!`)
          this._useMapLocation(successFn, errorFn, posOptions, this._s.fallBack)
        }
      } else if (type === ALIPAY) {
        // alipay
        this._useAlipayLocation(successFn, errorFn, posOptions, type)
      } else {
        this._useMapLocation(successFn, errorFn, posOptions, type)
      }
    })
  }

  /**
   * HTML5 GeolocationwatchPosition
   * @param {String} [type=h5] - type
   * @param {Function} sucCallback - sucCallback
   * @param {Function} errCallback - errCallback
   * @param {Function} options - options
   * @return {Number}
   * */
  watchPosition () {
    const noop = function () {}
    let type = ''
    let sucCallback = noop
    let errCallback = noop
    let options = {}

    /**
     * map
     * */
    const posOptions = {
      enableHighAccuracy: this._s.enableHighAccuracy,
      maximumAge: this._s.maximumAge,
      timeout: this._s.timeout
    }

    let args = Array.from(arguments)

    if (isString(args[0]) && isFunction(args[1])) {
      type = args[0]
      sucCallback = args[1]
      args[2] && (errCallback = args[2])
      args[3] && (options = args[3])
    }

    if (isFunction(args[0])) {
      type = 'h5'
      sucCallback = args[0]
      args[1] && (errCallback = args[1])
      args[2] && (options = args[2])
    }

    options = Object.assign({}, posOptions, options)

    if (type === H5 && this.isIos && !this.isHttps) {
      type = this._s.fallBack
      console.info('[MESSAGE]:WatchPosition() - > IOSHTTPS, \'' + type + '\'!')
    }

    /**
     * 
     * */
    const successFn = (pos) => {
      let position = {}
      switch (type) {
        case H5:
          position = {lat: pos.coords.latitude, lng: pos.coords.longitude, mapType: H5, full: pos}
          break
        case A_MAP:
          position = {lat: pos.position.lat, lng: pos.position.lng, mapType: A_MAP, full: pos}
          break
        case B_MAP:
          position = {lat: pos.point.lat, lng: pos.point.lng, mapType: B_MAP, full: pos}
          break
        case Q_MAP:
          position = {lat: pos.lat, lng: pos.lng, mapType: Q_MAP, full: pos}
          break
        default:
          position = {lat: pos.position.lat, lng: pos.position.lng, mapType: A_MAP, full: pos}
          break
      }
      sucCallback && sucCallback(position)
    }

    /**
     * 
     * */
    const errorFn = (err) => {
      let errType = Object.prototype.toString.call(err).match(/^(\[object )(\w+)\]$/i)[2].toLowerCase()
      if (errType === 'event') {
        console.warn(`[GEO]:  ${err.target.src}`)
        errCallback && errCallback(err)
        return
      }

      if (errType === 'string') {
        console.warn(`[GEO]: ${err}`)
        errCallback && errCallback(err)
        return
      }

      switch (type) {
        case H5:
          err = `[GEO]: CODE->${err.code}; TYPE->${ERROR_TYPE[err.code]}; MESSAGE->${err.message}`
          console.warn(err)
          break
        case A_MAP:
          err = `[GEO]: INFO->${err.info}; MESSAGE->${err.message}`
          console.warn(err)
          break
        case B_MAP:
          err = `[GEO]: ${JSON.stringify(err)}`
          console.warn(err)
          break
        case Q_MAP:
          err = `[GEO]: ${JSON.stringify(err)}`
          console.warn(err)
          break
        default:
          err = `[GEO]: INFO->${err.info}; MESSAGE->${err.message}`
          console.warn(err)
          break
      }
      errCallback && errCallback(err)
    }

    let watchId = null
    if (this._s[type] && this._s[type].key) {
      switch (type) {
        case H5:
          watchId = window.navigator.geolocation.watchPosition(successFn, errorFn, options)
          break
        case A_MAP:
          watchId = this._aMapWatchLocation(successFn, errorFn, options)
          break
        case B_MAP:
          console.error(' watchPosition() , !')
          break
        case Q_MAP:
          watchId = this._qMapWatchLocation(successFn, errorFn, options)
          break
        default:
          watchId = this._aMapWatchLocation(successFn, errorFn, options)
          break
      }
    } else {
      errCallback && errCallback(`'${type}'key!`)
    }
    return watchId
  }

  /**
   * HTML5 GeolocationclearWatch
   * */
  clearWatch (id) {
    if (!id) return
    window.navigator.geolocation.clearWatch(id)
    this._aMapGeolocation && this._aMapGeolocation.clearWatch(id)
    this._qMapGeolocation && this._qMapGeolocation.clearWatch(id)
  }

  // -------- private --------

  /**
   *  aMap  watchPosition()
   * @param {Function} sucCallback  - 
   * @param {Function} errCallback    - 
   * @param {Object} options   - 
   * @private
   * */
  _aMapWatchLocation (sucCallback, errCallback, options = {}) {
    if (this._aMapGeolocation) {
      return this._aMapGeolocation.watchPosition(sucCallback, errCallback, options)
    } else {
      console.warn(' aMapGeolocation ,  watchPosition()  getCurrentPosition(),  watchPosition()!')
      this._aMapLocation(sucCallback, errCallback, options)
      return 0
    }
  }

  /**
   *  qMap  watchPosition()
   * @param {Function} sucCallback  - 
   * @param {Function} errCallback    - 
   * @param {Object} options   - 
   * @private
   * */
  _qMapWatchLocation (sucCallback, errCallback, options = {}) {
    if (this._qMapGeolocation) {
      return this._qMapGeolocation.watchPosition(sucCallback, errCallback, options)
    } else {
      console.warn(' qMapWatchLocation ,  watchPosition()  getCurrentPosition(),  watchPosition()!')
      this._qMapLocation(sucCallback, errCallback, options)
      return 0
    }
  }

  /**
   * alipay
   * @param {Function} sucCallback  - 
   * @param {Function} errCallback    - 
   * @param {Object} options   - 
   * @private
   * */
  _useAlipayLocation (sucCallback, errCallback) {
    window.AlipayJSBridge && window.AlipayJSBridge.call('getLocation', function (result) {
      if (result.error) {
        errCallback(result.errorMessage)
        return
      }
      // cityresult.provinceresult.city?result.city:result.provincecity
      sucCallback(result)
    })
  }

  /**
   *   
   * @param {Function} successFn  - 
   * @param {Function} errorFn    - 
   * @param {Object} posOptions   - 
   * @param {string} type         - map
   * @private
   * */
  _useMapLocation (successFn, errorFn, posOptions = {}, type) {
    if (this._s[type].key) {
      switch (type) {
        case A_MAP:
          this._aMapLocation(successFn, errorFn, posOptions)
          break
        case B_MAP:
          this._bMapLocation(successFn, errorFn, posOptions)
          break
        case Q_MAP:
          this._qMapLocation(successFn, errorFn, posOptions)
          break
        default:
          this._aMapLocation(successFn, errorFn, posOptions)
          break
      }
    } else {
      errorFn('The map type of \'' + type + '\' must have key!')
    }
  }

  /**
   *  aMap 
   * @param {Function} successFn  - 
   * @param {Function} errorFn    - 
   * @param {Object} posOptions   - 
   * @see http://lbs.amap.com/api/javascript-api/reference/location/
   * @private
   * */
  _aMapLocation (successFn, errorFn, posOptions = {}) {
    let _getCurrentPosition = () => {
      this._aMapGeolocation.getCurrentPosition((status, result) => {
        if (status === 'complete') {
          successFn && successFn(result)
        } else {
          errorFn && errorFn(status)
        }
      })
    }

    if (!this._aMapGeolocation) {
      this._getScript('//webapi.amap.com/maps?v=1.3&key=' + this._s[A_MAP].key).then(() => {
        if (typeof window.AMap === 'undefined') return
        let map = new window.AMap.Map('')
        map.plugin('AMap.Geolocation', () => {
          /**
           * @param {object} posOptions - 
           * @param {boolean} [posOptions.enableHighAccuracy=true] - :true
           * @param {number} [posOptions.timeout=10000] - 10
           * @param {number} 9posOptions.maximumAge=00 - 00
           * */
          this._aMapGeolocation = new window.AMap.Geolocation(posOptions)
          _getCurrentPosition()
        })
      }, (err) => { errorFn && errorFn(err) })
    } else {
      _getCurrentPosition()
    }
  }

  /**
   *  bMap 
   * @param {Function} successFn  - 
   * @param {Function} errorFn    - 
   * @param {Object} posOptions   - 
   * @see http://developer.baidu.com/map/reference/index.php?title=Class:%E6%9C%8D%E5%8A%A1%E7%B1%BB/Geolocation
   * @private
   * */
  _bMapLocation (successFn, errorFn, posOptions = {}) {
    let _getCurrentPosition = () => {
      this._bMapGeolocation.getCurrentPosition(function (pos) {
        if (this.getStatus() === window.BMAP_STATUS_SUCCESS) {
          successFn && successFn(pos)
        } else {
          errorFn && errorFn(this.getStatus())
        }
      }, posOptions)
    }

    if (!this._bMapGeolocation) {
      this._getScript('//api.map.baidu.com/getscript?v=2.0&ak=' + this._s[B_MAP].key + '&services=&t=' + (+new Date())).then(() => {
        if (!window.BMap) return
        this._bMapGeolocation = new window.BMap.Geolocation()
        _getCurrentPosition()
      }, (err) => { errorFn && errorFn(err) })
    } else {
      _getCurrentPosition()
    }
  }

  /**
   *  qMap 
   * @param {Function} successFn  - 
   * @param {Function} errorFn    - 
   * @param {Object} posOptions   - 
   * @see http://lbs.qq.com/tool/component-geolocation.html
   * @private
   * */
  _qMapLocation (successFn, errorFn, posOptions = {}) {
    let _getCurrentPosition = () => {
      this._qMapGeolocation.getLocation(successFn, errorFn, {
        timeout: posOptions.timeout
      })
    }

    if (!this._qMapGeolocation) {
      this._getScript('//3gimg.qq.com/lightmap/components/geolocation/geolocation.min.js').then(() => {
        /**
         * Geolocation
         * @params {string} key - (key)
         * @params {string} referer - 
         * */
        if (!window.qq) return
        this._qMapGeolocation = new window.qq.maps.Geolocation(this._s[Q_MAP].key, this._s[Q_MAP].name)
        _getCurrentPosition()
      }, (err) => { errorFn && errorFn(err) })
    } else {
      _getCurrentPosition()
    }
  }

  /**
   *  H5 
   * @param {Function} successFn  - 
   * @param {Function} errorFn    - 
   * @param {Object} posOptions   - 
   * @private
   * */
  _h5Location (successFn, errorFn, posOptions = {}) {
    window.navigator.geolocation.getCurrentPosition(successFn, errorFn, posOptions)
  }

  /**
   * 
   * @param  {[String]} mapJsUrl [map js url]
   * @return {Promise}
   * @private
   */
  _getScript (mapJsUrl) {
    return new Promise((resolve, reject) => {
      let sc = document.createElement('script')
      sc.type = 'text/javascript'
      sc.src = mapJsUrl
      sc.onload = sc.onreadystatechange = function () {
        if (!this.readyState || /^(loaded|complete)$/.test(this.readyState)) {
          resolve()
          sc.onload = sc.onreadystatechange = null
        }
      }
      sc.onerror = function (err) {
        reject(err)
        sc.onerror = null
      }
      document.body.appendChild(sc)
    })
  }
}

export default {
  version: VERSION,
  installed: false,
  install (Vue, options) {
    if (this.installed) return
    Vue.prototype['$geo'] = new Geolocation(options)
    this.installed = true
  }
}


