<template>
    <Page>
        <Header>
            <Navbar>
                <Title>Platform</Title>
            </Navbar>
        </Header>
        <Content padding class="outer-content">
            <h5></h5>
            <p>
                , <strong></strong>: IOS/Andriod, <strong>HyBrid</strong>: Wechat/Alipay/DingTalk/QQ/DaHan.
            </p>

            <h5></h5>
            <p>HyBrid, ChooseImg/ScanCode. . , $platform.ready().</p>
            <Grid>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>{{$platform._bPlt}}</Column>
                </Row>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>{{$platform._dir}}</Column>
                </Row>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>{{$platform._lang}}</Column>
                </Row>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>{{$platform._nt || ''}}</Column>
                </Row>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>
                        {{$platform._isPortrait === null ? '' : $platform._isPortrait === true ? '' : ''}}
                    </Column>
                </Row>

                <!---->
                <Row v-if="$platform._isPortrait !== null">
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8 v-if="$platform._isPortrait">
                        <Row>: {{$platform._pH}}px</Row>
                        <Row>: {{$platform._pW}}px</Row>
                    </Column>
                    <Column col-8 v-if="!$platform._isPortrait">
                        <Row>: {{$platform._lH}}px</Row>
                        <Row>: {{$platform._lW}}px</Row>
                    </Column>
                </Row>

                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>
                        <strong>{{$platform._platforms.join(' -> ')}}</strong>
                    </Column>
                </Row>

                <Row>
                    <Column col-4><strong>:</strong></Column>

                    <Column col-8 v-if="JSON.stringify($platform._qp.data) !== '{}'">
                        <div class="detailBox" v-for="(value,key) in $platform._qp.data">
                            <Row><span class="detailBox__title">{{key}}: </span><span
                                    class="detailBox__value">{{value}}</span></Row>
                        </div>
                    </Column>
                    <Column col-8 v-else>
                        <Row></Row>
                    </Column>
                </Row>

                <Row>
                    <Column col-4><strong>UserAgent:</strong></Column>
                    <Column col-8>{{$platform._ua}}</Column>
                </Row>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>
                        <div class="detailBox" v-for="(value,key) in $platform._versions" v-if="!!value">
                            <Row><span class="detailBox__title">{{key}}: </span><span
                                    class="detailBox__value">{{value.str}}</span>
                            </Row>
                        </div>
                    </Column>
                </Row>
                <Row>
                    <Column col-4><strong>CSS:</strong></Column>
                    <Column col-8>
                        <div class="detailBox" v-for="(value,key) in $platform.css">
                            <Row><span class="detailBox__title"><strong>{{key}}:</strong></span></Row>
                            <Row><span class="detailBox__value">{{value}}</span></Row>
                        </div>
                    </Column>
                </Row>

                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>
                        <div class="detailBox" v-for="key in thisKeysOfRegisterMethod"
                             v-if="thisKeysOfRegisterMethod.length > 0">
                            <Row><span class="detailBox__title"><strong>{{key}}</strong></span></Row>
                        </div>
                        <div class="detailBox" v-if="thisKeysOfRegisterMethod.length === 0"></div>
                    </Column>
                </Row>
            </Grid>
        </Content>
    </Page>
</template>
<style scoped lang="scss">

</style>
<script type="text/javascript">
  export default{
    name: 'name',
    data () {
      return {
        thisKeysOfRegisterMethod: []
      }
    },
    props: {},
    watch: {},
    computed: {},
    methods: {},
    created () {},
    mounted () {
      this.$platform.ready().then(() => {
        this.thisKeysOfRegisterMethod = Object.keys(this.$platform.registerMethod())
      }, () => {})
    },
    activated () {},
    components: {}
  }
</script>



