<template>
    <div class="action-sheet" :class="[modeClass,cssClass]">
        <!--backdrop-->
        <Backdrop :bdClick="bdClick" :enableBackdropDismiss="enableBackdropDismiss"
                  :isActive="isActive"></Backdrop>
        <!--actionsheet wrap-->
        <transition
                name="action-sheet"
                @before-enter="beforeEnter"
                @after-enter="afterEnter"
                @before-leave="beforeLeave"
                @after-leave="afterLeave">
            <div class="action-sheet-wrapper" v-show="isActive">
                <div class="action-sheet-container">
                    <!--group normal-->
                    <div class="action-sheet-group">
                        <div class="action-sheet-title" v-if="title">
                            <span>{{title}}</span>
                            <div class="action-sheet-sub-title" v-if="subTitle">{{subTitle}}</div>
                        </div>
                        <div class="action-sheet-buttons">
                            <Button role="action-sheet-button" @click="click(b)" v-for="(b,index) of normalButtons"
                                    :key="index"
                                    :class="[b.cssClass,{'icon-left':b.icon}]">
                                <Icon :name="b.icon" v-if="b.icon" class="action-sheet-icon"></Icon>
                                <span>{{b.text}}</span>
                            </Button>
                        </div>
                    </div>
                    <!--group cancel-->
                    <div class="action-sheet-group" v-if="cancelButton">
                        <Button role="action-sheet-button" @click="click(cancelButton)"
                                class="action-sheet-cancel" :class="cancelButton.cssClass">
                            <Icon :name="cancelButton.icon" v-if="cancelButton.icon"
                                  class="action-sheet-icon"></Icon>
                            <span>{{cancelButton.text || 'Cancel'}}</span>
                        </Button>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script type="text/javascript">
  /**
   * @component ActionSheet
   * @description
   *
   * ##  / ActionSheet
   *
   * ### 
   *
   * ActionSheetButton. ****, ****, 6, , .
   *
   *
   * ### buttons
   * - role: cancel()/destructive(/)/null, destructiveIOS, 
   * - icon: Icon
   *
   * ### 
   *
   * - App, , loading/toast.
   * - , , `dismissOnPageChange`.
   * - , `enableBackdropDismiss`.
   * - Promise, , setTimeout, transitionEnd, .
   *
   * @props {String} [title]                        - ActionSheet
   * @props {string} [subTitle]                     - ActionSheet
   * @props {string} [cssClass]                     - Additional classes for custom styles, separated by spaces
   * @props {Boolean} [enableBackdropDismiss=true]  - backdropactionsheet
   * @props {Boolean} [dismissOnPageChange=true]    - 
   * @props {string} [mode=ios]                     - 
   * @props {Array} [buttons]                       - buttonrole
   * @props {Array} buttons.text                       - button
   * @props {Array} buttons.icon                       - buttoniconname, Icon
   * @props {Array} buttons.role                       - cancel()/destructive(/)/null
   * @props {Array} buttons.handler                    - 
   * @props {Array} buttons.cssClass                   - 
   *
   * @demo https://dtfe.github.io/vimo-demo/#/action-sheet
   * @usage
   *
   * this.$actionSheet.present({
   *  title: '',
   *  subTitle: '',
   *  cssClass: 'ActionSheetCssClass1 ActionSheetCssClass2',
   *  enableBackdropDismiss: true,
   *  buttons: [
   *    {
   *      text: '',
   *      role: 'destructive',
   *      icon: 'trash',
   *      cssClass: '  DestructiveBtnCssClass1 DestructiveBtnCssClass2 ',
   *      handler: () => {
   *        console.log('Destructive clicked');
   *      }
   *    },
   *    {
   *      text: '',
   *      icon: 'share',
   *      handler: () => {
   *        console.log('Archive1 clicked');
   *      }
   *    },
   *    {
   *      text: '',
   *      icon: 'play',
   *      handler: () => {
   *        console.log('Archive4 clicked');
   *      }
   *    },
   *    {
   *      text: '',
   *      role: 'cancel',
   *      icon: 'close',
   *      handler: () => {
   *        this.$actionSheet.dismiss().then(function (data) {
   *          console.debug('promise')
   *        });
   *      }
   *    }
   *  ]
   * })
   *
   */
  import { urlChange } from '../../util/util'
  import { Backdrop } from '../backdrop'
  import { Button } from '../button'
  import { Icon } from '../icon'
  export default{
    name: 'ActionSheet',
    props: {
      title: [String],
      subTitle: [String],
      cssClass: [String],
      buttons: {
        type: Array,
        default () { return [] }
      },
      enableBackdropDismiss: {
        type: Boolean,
        default () { return true }
      },
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      },
      dismissOnPageChange: {
        type: Boolean,
        default () { return true }
      }
    },
    data () {
      return {
        /**
         * ActionSheet State
         * @private
         * */
        isActive: false,    // ActionSheet 
        enabled: false,     // false

        /**
         * ActionSheet 
         * computed
         * watch
         * @private
         * */
        normalButtons: [],  // 
        cancelButton: null, // ()

        // promise
        presentCallback: null,
        dismissCallback: null,

        unreg: null         // 
      }
    },
    computed: {
      // ActionSheet
      modeClass: function () {
        return `action-sheet-${this.mode}`
      }
    },
    methods: {
      /**
       * ActionSheet Animate Hooks
       * @private
       * */
      beforeEnter () {
        this.enabled = false // 
        this.$app && this.$app.setEnabled(false, 400)
      },
      afterEnter () {
        this.presentCallback()
        this._focusOutActiveElement()
        let focusableEle = document.querySelector('button')
        if (focusableEle) {
          focusableEle.focus()
        }
        this.enabled = true
      },
      beforeLeave () {
        this.enabled = false
        this.$app && this.$app.setEnabled(false, 400)
      },
      afterLeave () {
        this.dismissCallback()
        // DOM
        this.$el.remove()
        this.enabled = true
      },

      /**
       * ActionSheetfocus
       * @private
       * */
      _focusOutActiveElement () {
        // only button
        const activeElement = document.activeElement
        activeElement && activeElement.blur && activeElement.blur()
      },

      /**
       * @function bdClick
       * @description
       * backdrop,actionsheet
       *
       * cancelactionsheet
       * Backdrop Click Handler, If cancelButton defined, then action cancelButton handler.
       * @private
       */
      bdClick () {
        if (this.enabled && this.enableBackdropDismiss) {
          if (this.cancelButton) {
            this.click(this.cancelButton)
          } else {
            this.dismiss()
          }
        }
      },

      /**
       * @function click
       * @description
       * 
       * @param {object} button Button Click Handler
       * @private
       */
      click (button) {
        if (!this.enabled) {
          return
        }
        let shouldDismiss = true
        if (button.handler) {
          // a handler has been provided, execute it
          if (button.handler() === false) {
            // if the return value of the handler is false then do not dismiss
            shouldDismiss = false
          }
        }

        // (dismissing)
        // dismissing
        // 
        if (this.enabled && shouldDismiss) {
          this.dismiss()
        }
      },

      /**
       * @function present
       * @description
       * ActionSheet
       * @param {Object} options - props, actionsheet.js
       * @returns {Promise}  Promise, resolved
       */
      present () {
//        console.log('action-sheet.vue present')
        this.isActive = true
        return new Promise((resolve) => { this.presentCallback = resolve })
      },

      /**
       * @function dismiss
       * @description
       * ActionSheet
       * @return {Promise} Promise, resolved
       * */
      dismiss () {
        if (this.isActive) {
          this.isActive = false // 
          this.unreg && this.unreg()
          if (!this.enabled) {
            this.$nextTick(() => {
              this.dismissCallback()
              this.$el.remove()
              this.enabled = true
            })
          }
          return new Promise((resolve) => { this.dismissCallback = resolve })
        } else {
          return new Promise((resolve) => { resolve() })
        }
      },

      /**
       * buttons
       * @private
       * */
      init () {
        let arr = this.buttons
        let _buttons = []
        if (!Array.isArray(arr)) {
          return
        }
        arr.forEach((button) => {
          if (typeof button === 'string') {
            button = {text: button}
          }

          if (!button.cssClass) {
            button.cssClass = ''
          } else {
            // 
            button.cssClass = button.cssClass.trim()
          }

          if (button.role === 'cancel') {
            this.cancelButton = button
          } else {
            if (button.role === 'destructive') {
              button.cssClass = (button.cssClass + ' ' || '') + 'action-sheet-destructive'
            } else if (button.role === 'selected') {
              button.cssClass = (button.cssClass + ' ' || '') + 'action-sheet-selected'
            }
            _buttons.push(button)
          }
        })
        this.normalButtons = _buttons
      }
    },
    created () {
      this.init()
      // mounted before data ready, so no need to judge the `dismissOnPageChange` value
      if (this.dismissOnPageChange) {
        this.unreg = urlChange(() => {
          this.isActive && this.dismiss()
        })
      }
    },
    components: {
      Backdrop,
      Button,
      Icon
    }
  }
</script>
<style lang="scss">
    @import './action-sheet.scss';
    @import './action-sheet.ios.scss';
    @import './action-sheet.md.scss';

    // transitioName = 'action-sheet'
    .action-sheet-enter-active, .action-sheet-leave-active {
        transform: translateY(0%);
    }

    .action-sheet-enter, .action-sheet-leave-active {
        transform: translateY(100%);
    }
</style>



