<template>
    <div class="ion-note" :class="[modeClass,colorClass]">
        <slot></slot>
    </div>
</template>
<style lang="scss">
    @import 'note.ios.scss';
    @import 'note.md.scss';
</style>
<script type="text/javascript">
  /**
   * @component Note
   * @description
   *
   * ##  / note
   *
   * 10%, .
   *
   * @props {String} color - 
   * @props {String} [mode='ios'] - 
   *
   * */
  export default{
    name: 'Note',
    props: {
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: [String],
      /**
       * mode 
       * */
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      }
    },
    computed: {
      // 
      modeClass () {
        return `note note-${this.mode}`
      },
      // 
      colorClass () {
        return this.color ? (`note-${this.mode}-${this.color}`) : ''
      }
    }
  }
</script>



