<template>
    <nav class="ion-nav"
         :class="[menuContentClass,menuContentTypeClass,{'menu-content-open':isMenuOpen}]"
         :style="menuStyleObj">
        <div nav-viewport></div>
        <div v-if="isMenuOpen" @click="tapToCloseMenu" class="click-cover"></div>
        <!--animate-->
        <transition :name="pageTransitionName">
            <slot></slot>
        </transition>
        <div class="nav-decor"></div>
    </nav>
</template>
<style lang="scss">

    .ion-nav {
        .click-cover {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 9999;
        }
    }
</style>
<script type="text/javascript">
  /**
   * @component Base/Nav
   * @description
   *
   * ##  / Nav
   *
   * Page, .
   *
   * css3, Vimo. props, , , , App.
   *
   * @props {String} [pageTransition] - , : ios-transition/zoom-transition/fade-bottom-transition/fade-right-transition/fade-transition
   * @props {Boolean} [showIndicatorWhenPageChange=true] - Indicator
   *
   * */
  import { Indicator } from 'vimo/components/indicator'
  export default{
    name: 'Nav',
    props: {
      // 
      // ios-transition/fade-bottom-transition/zoom-transition/fade-right-transition/fade-transition
      pageTransition: {
        type: String,
        default () { return this.$config && this.$config.get('pageTransition') }
      },
      // Indicator
      showIndicatorWhenPageChange: {
        type: Boolean,
        default () { return this.$config && this.$config.getBoolean('showIndicatorWhenPageChange') }
      }
    },
    data () {
      return {
        // ----------- Nav -----------
        pageTransitionName: null,

        // ----------- Menu -----------
        menuStyleObj: {
          transform: ''
        },

        isMenuOpen: false, // ion-menu
        menuId: null, // menuId
        menuType: '', // overlay/reveal/push   reveal/push
        menuSide: 'left', // 
        menuContentClass: null,
        menuContentTypeClass: null,
        transform: this.$platform.css ? this.$platform.css.transform : 'webkitTransform'
      }
    },
    methods: {
      // -------- Nav --------
      /**
       * 
       * @private
       * */
      initNav () {
        // nav 
        this.$eventBus.$on('onNavEnter', ({to, from, next}) => {
          this.pageTransitionName = `${this.pageTransition}-forward`
          this.$app && this.$app.setEnabled(false, 500)
          next()
        })
        this.$eventBus.$on('onNavLeave', ({to, from, next}) => {
          this.pageTransitionName = `${this.pageTransition}-backward`
          this.$app && this.$app.setEnabled(false, 500)
          next()
        })

        // Indicator
        if (this.showIndicatorWhenPageChange) {
          this.$router.beforeEach((to, from, next) => {
            Indicator.present()
            next()
          })

          this.$router.afterEach(() => {
            Indicator.dismiss()
          })
        }

        // history
        this.$history._init()
      },

      // ----------- Menu -----------
      /**
       * navMenu
       * @private
       * */
      tapToCloseMenu () {
        this.$nextTick(() => {
          this.isMenuOpen && this.$menus.close()
        })
      },

      /**
       * Menu
       * @private
       * */
      setMenuInfo (menuId) {
        if (menuId) {
          this.menuId = menuId
          this.menuSide = this.$menus.menuIns[menuId].side
          this.menuType = this.$menus.menuIns[menuId].type
          this.menuContentClass = `menu-content`
          this.menuContentTypeClass = `menu-content-${this.menuType}`
        }
      },

      /**
       * menu
       * @private
       * */
      initMenu () {
        let _translateX
        // menu
        this.$eventBus.$on('onMenuOpen', (menuId) => {
          this.setMenuInfo(menuId)
          this.isMenuOpen = true

          // , 340px264px
          // 340px304px
          if (this.$platform.width() > 340) {
            _translateX = 304
          } else {
            _translateX = 264
          }

          if (this.menuType === 'reveal' || this.menuType === 'push') {
            if (this.menuSide === 'left') {
              this.menuStyleObj[this.transform] = `translateX(${_translateX}px)`
            } else {
              this.menuStyleObj[this.transform] = `translateX(-${_translateX}px)`
            }
          }
        })
        this.$eventBus.$on('onMenuClosing', (menuId) => {
          this.isMenuOpen = false
          if (this.menuType === 'reveal' || this.menuType === 'push') {
            this.menuStyleObj[this.transform] = 'translateX(0)'
          }
        })
        this.$eventBus.$on('onMenuClosed', () => {
          this.menuContentTypeClass = null
        })
      }
    },
    created () {
      // menu
      this.initMenu()

      //  
      this.initNav()
    },
    mounted () {

    }
  }
</script>



