<template>
    <transition
            name="backdrop"
            @before-enter="beforeEnter"
            @after-leave="afterLeave">
        <div class="ion-backdrop"
             @click="bdClick"
             @touchmove="onTouchMoveHandler($event)"
             :class="{'backdrop-no-tappable':!enableBackdropDismiss,'fixed':fixed}"
             :style="{'left':left+'px','top':top+'px'}"
             v-show="isActiveLocal"></div>
    </transition>
</template>
<style lang="scss">
    @import './backdrop';

    // transitioName = 'backdrop'
    .backdrop-enter-active, .backdrop-leave-active {
        transition: opacity 200ms;
    }

    .backdrop-enter, .backdrop-leave-active {
        opacity: 0
    }
</style>
<script type="text/javascript">
  /**
   * @component Backdrop
   * @description
   *
   *
   * ##  / Backdrop
   *
   * . Alert/Actionsheet.
   *
   * @props {Boolean} [enableBackdropDismiss=true] - , `backdrop-no-tappable` ()
   * @props {Boolean} [isActive=false] - 
   * @props {Function} [bdClick=noop] - 
   * @props {Number} [top=0] - 
   * @props {Number} [left=0] - 
   * @props {Boolean} [fixed=false] - positionfixed, 
   *
   * @fires component:Backdrop#onShown
   * @fires component:Backdrop#onHidden
   *
   * @demo https://dtfe.github.io/vimo-demo/#/backdrop
   *
   * @usage
   * <Backdrop :bdClick="bdClick" :enableBackdropDismiss="enableBackdropDismiss" :isActive="isActive"></Backdrop>
   * */
  export default{
    name: 'Backdrop',
    data () {
      return {
        // 
        isActiveLocal: this.isActive, // present/dismiss
        count: 0 // 
      }
    },
    props: {
      /**
       * 
       * */
      enableBackdropDismiss: {
        type: Boolean,
        default: true
      },
      /**
       * backdrop
       * */
      isActive: [Boolean],
      /**
       * 
       * */
      bdClick: {
        type: Function,
        default () {
          return function () {}
        }
      },
      /**
       * backdrop, 
       * */
      top: {
        type: Number,
        default: 0
      },
      left: {
        type: Number,
        default: 0
      },
      // positionfixed, 
      fixed: [Boolean]
    },
    watch: {
      isActive () {
        this.isActiveLocal = this.isActive
      }
    },
    methods: {
      /**
       * 
       * @private
       */
      beforeEnter () {
        /**
         * @event component:Backdrop#onShown
         * @description Backdrop
         */
        this.$emit('onShown')
      },
      afterLeave () {
        /**
         * @event component:Backdrop#onHidden
         * @description Backdrop
         */
        this.$emit('onHidden')
      },
      onTouchMoveHandler ($event) {
        $event.preventDefault()
        $event.stopPropagation()
      }
    },
  }
</script>



