<template>
    <Page>
        <Header>
            <Navbar>
                <Title>Nav</Title>
            </Navbar>
        </Header>
        <Content padding class="outer-content">
            <h5></h5>
            <p>NavPage, (animate)/Indicator(showIndicatorWhenPageChange). , . , . </p>

            <h5></h5>
            <p>
                , :
                ios-transition/zoom-transition/fade-bottom-transition/fade-right-transition
            </p>

            <h5></h5>
            <p>Indicator, Indicator, . , .</p>

            <h5></h5>
            <Grid>
                <Row>
                    <Column col-4><strong>IOS:</strong></Column>
                    <Column col-8>fade-right-transition</Column>
                </Row>
                <Row>
                    <Column col-4><strong>Android:</strong></Column>
                    <Column col-8>zoom-transition</Column>
                </Row>
            </Grid>

            <h5>Nav</h5>
            <Grid>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>{{$config.get('pageTransition')}}</Column>
                </Row>
                <Row>
                    <Column col-4><strong>:</strong></Column>
                    <Column col-8>{{$config.get('showIndicatorWhenPageChange')}}</Column>
                </Row>
            </Grid>

        </Content>
    </Page>
</template>
<style scoped lang="scss">
    .detailBox {
        margin-bottom: 5px;

        .detailBox__title {
            color: #000;
            font-weight: bold;
        }
        .detailBox__value {
            color: #333;
        }
    }
</style>
<script type="text/javascript">
  export default{
    name: 'name',
    data () {
      return {}
    },
    props: {},
    watch: {},
    computed: {},
    methods: {},
    created () {},
    mounted () {

      this.$config.get('pageTransition')

    },
    activated () {},
    components: {}
  }
</script>



