<template>
    <header class="ion-header header" :class="[modeClass,{'hide-bar':isHide}]" :style="style">
        <slot></slot>
    </header>
</template>
<style lang="scss">
    @import "../../themes/globals";

    .ion-header {
        position: absolute;
        top: 0;
        left: 0;
        z-index: $z-index-toolbar;
        display: block;
        width: 100%;
        transition: -webkit-transform ease 300ms;
    }

    .ion-header.hide-bar {
        transform: translateY(-100%);
    }
</style>
<script type="text/javascript">
  /**
   * @component Base/Header
   * @description
   *
   * ##  / Footer
   *
   * HeaderFooter, , , .
   *
   * HeaderVimo, Toolbar/Navbar/,
   * , ContentHeader`margin`, `padding`.
   *
   * ### 
   * - [no-border] - 
   *
   * @props {String} [mode='ios'] - 
   *
   * @see component:Base/Footer
   *
   * */
  export default{
    name: 'Header',
    props: {
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      }
    },
    computed: {
      modeClass () {
        return `header-${this.mode}`
      }
    },
    data () {
      return {
        // -------- public --------
        isHide: false,
        style: {}
      }
    },
    methods: {
      // -------- public --------
      /**
       * @function hide
       * @description
       * Header
       * */
      hide () {
        this.isHide = true
      },

      /**
       * @function show
       * @description
       * Header
       * */
      show () {
        this.isHide = false
      },

      /**
       * @function toggle
       * @description
       * ToggleHeader
       * */
      toggle () {
        this.isHide = !this.isHide
      },

      /**
       * @function setStyle
       * @param {object} style - 
       * @see https://cn.vuejs.org/v2/guide/class-and-style.html#-1
       * @description
       * Header
       * */
      setStyle (style) {
        this.style = style
      }
    }
  }
</script>



