/**
 * Created by Hsiang on 2017/3/6.
 *
 * App
 *
 * $logapp
 *
 * @property {options} -  
 * @property {boolean=false} options.needLogPage  - , false, `this.$log.init()`
 *
 *
 * ## console
 *
 * logconsole, console, , console, !
 *
 *
 * ## 
 *
 * , `window.onerror``window.addEventListener('error', function (err) {})`, try/catch.
 *
 *
 * ## try/catcherr
 *
 * , err, err, apiapi, , , : console.error(err).
 *
 * ```
 * try {
 *    window.decodeURI('%2')
 * } catch (err) {
 *    console.error(err)
 * }
 * ```
 *
 *
 * ## 
 *
 * ```
 * import log from './log'
 * Vue.use(log, {
 *  needLogPage: false, // log
 * })
 * ```
 *
 * ## 
 *
 * ### 1. log
 *
 *```
 * this.$log.init() // 
 *```
 *
 * ### 2. log
 *
 *```
 * this.$log.open()
 * this.$log.close()
 *```
 *
 * ### 3. 
 *
 *```
 * console.log(string)
 * this.$log.log(string)
 *
 * console.debug(string)
 * this.$log.debug(string)
 *
 * console.info(string)
 * this.$log.info(string)
 *
 * console.warn(any) // , 
 * this.$log.warn(any)
 *
 * console.error(any)  // , 
 * this.$log.error(any)
 *
 * console.assert(boolean, any) // , false, error
 * this.$log.assert(boolean,any)
 *```
 *
 * ## error/warn/assertany
 *
 * 1. error, try/catcherr
 * 2. string, 
 * 3. string: message, errorName, script, line, 
 *
 * ```
 * console.error('ajax','AJAX TIMEOUT/FAIL','./getData.js::<Function>getInfo()','12')
 * console.assert(false, ', String','AJAX ASSERT','./getData.js::<Function>getInfo()','12')
 * ```
 *
 * ### 4. 
 *
 * ```
 * this.$log.getHistory
 * ```
 *
 */
const HAS_CONSOLE = typeof window.console !== 'undefined'

export default{
  version: '0.0.1',
  installed: false,
  /**
   * @param {object} Vue - Vue
   * @param {object} options - 
   * @param {boolean=true} options.needLogPage  - 
   * */
  install (Vue, options = {}) {
    if (this.installed) return
    let _ins = new Log(Vue, options)
    Vue.prototype.$log = _ins

    // console
    // log: log/debug/info
    // log: error/warn/assert , 
    if (HAS_CONSOLE) {
      let console = window.console
      let typeLists = ['log', 'debug', 'info', 'error', 'warn', 'assert']
      typeLists.forEach((type) => {
        let typeFn = console[type].bind(console)
        console[type] = function () {
          var args = Array.prototype.slice.call(arguments)
          // ---------- !! ---------
          // console
          // ---------- !! ---------
          typeFn.apply(null, arguments)
          _ins[type](...args)
        }
      })
    }

    /**
     * js
     * @param {String}  message   
     * @param {String}  script      
     * @param {String}    line     
     * @param {String}    column   
     * @param {Object}  errorObj       Anything
     */
    window['onerror'] = function (message, script, line, column, errorObj) {
      _ins.error(normalizeError({
        message: message,
        name: !!errorObj && (errorObj.message || message.split(':')[1].trim()),
        script: script,
        line: line,
        stack: !!errorObj && errorObj.stack
      }))
      return true
    }

    // : Script error.
    window.addEventListener('error', function (err) {
      _ins.error(normalizeError(err))
    })
    this.installed = true
  }
}

/**
 * @name Log
 * @description
 * 
 * @param {object} options - 
 * */
class Log {
  constructor (Vue, options) {
    this._v = Vue
    this._isLogPageInit = false // log
    this._logPageInstance = null // log
    this._history = []// 

    if (options.needLogPage) {
      this.init()
    }
  }

  // -------- interface --------

  /**
   * 
   * */
  init () {
    if (!this._isLogPageInit) {
      let LogConstructor = this._v.extend(require('./log.vue'))
      this._isLogPageInit = true
      this._logPageInstance = new LogConstructor({
        // body
        el: document.body.appendChild(document.createElement('div'))
      })
    }
    this._logPageInstance.setRecordList(this._history)
    return this._logPageInstance
  }

  /**
   * 
   * */
  open () {
    this._logPageInstance && this._logPageInstance.open()
  }

  /**
   * 
   * */
  close () {
    this._logPageInstance && this._logPageInstance.close()
  }

  // -------- normal logs --------
  /**
   * log
   * @param {any} msg - 
   * @return {string} - msg
   * */
  log (msg) {
    return this._recordTypeAssemble('log', msg)
  }

  /**
   * debug
   * @param {any} msg - 
   * @return {string} - msg
   * */
  debug (msg) {
    return this._recordTypeAssemble('debug', msg)
  }

  /**
   * info
   * @param {any} msg - 
   * @return {string} - msg
   * */
  info (msg) {
    return this._recordTypeAssemble('info', msg)
  }

  // -------- abnormal logs --------

  /**
   * error
   * 1. objectnormalizeError
   * 2. message
   * 3. message-errorName-script-line
   *
   * @example:
   *
   * try{
   *  a()
   * }catch(rawError){
   *  this.$log.error(rawError)
   * }
   *
   * this.$log.error('')
   *
   * // : msg/errName/script/line
   * this.$log.error('ajax','AJAX TIMEOUT/FAIL','./getData.js::<Function>getInfo()','12')
   *
   * */
  error () {
    return this._recordTypeAssemble('error', ...arguments)
  }

  /**
   * warn
   * error
   * error
   * */
  warn () {
    return this._recordTypeAssemble('warn', ...arguments)
  }

  /**
   * 
   * @param {boolean} isFalse - 
   * error
   * */
  assert (isFalse) {
    if (!isFalse) {
      let args = Array.prototype.slice.call(arguments)
      args.shift()
      return this._recordTypeAssemble('assert', ...args)
    }
  }

  // -------- logs history --------

  /**
   * log
   * @return {array}
   * */
  getHistory () {
    return this._history
  }

  // -------- private --------

  /**
   * @private
   * 
   * @param {string} type             - 
   * @param {(any|object)} message    - /err
   * @param {any=} errorName     - 
   * @param {string=} script     - , 
   * @param {string=} line       - 
   * @param {string=} stack     - 
   * */
  _recordTypeAssemble (type, message, errorName, script, line, stack) {
    let args = Array.prototype.slice.call(arguments)
    if (args.length === 2) {
      if (type === 'error' || type === 'warn' || type === 'assert') {
        let abnormalType = Object.prototype.toString.call(args[1]).match(/^(\[object )(\w+)\]$/i)[2].toLowerCase()
        if (abnormalType === 'object' || abnormalType === 'error') {
          // _recordTypeAssemble('error', rawErr)
          let formatted = normalizeError(args[1])
          message = formatted['message']
          errorName = formatted['name']
          script = formatted['script']
          line = formatted['line']
          stack = formatted['stack']
        } else {
          message = args[1].toString()
        }
      }
    }

    if (args.length > 2) {
      // _recordTypeAssemble('error','ajax','AJAX TIMEOUT/FAIL','./getData.js::<Function>getInfo()','12')
      message = args[1]
      errorName = args[2]
      script = args[3]
      line = args[4]
      stack = args[5]
    }

    let _str = this._msgToString(message)
    !!_str && this._storeRecord({
      message: _str,                // The error message
      count: 1,                     // the same message count
      type: type,                   // 
      name: errorName,              // 
      time: (new Date()).getTime(), // record time
      script: script,               // The script file the error occured in
      stack: stack,                 // Stack trace (as a string)
      line: line                   // The line number the error occured on
    })

    return _str
  }

  /**
   * @private
   * msgstring
   * @param {any} msg - 
   * @return {(string|boolean)} false
   * */
  _msgToString (msg) {
    if (!msg) {
      return false
    }

    if (typeof msg === 'object') {
      try {
        msg = JSON.stringify(msg)
      } catch (err) {}
    }

    if (typeof msg !== 'string') {
      msg = msg.toString()
    }

    return msg
  }

  /**
   * @private
   * console, , ++
   * @param {object} record - 
   * @param {string} record.message - string
   * @param {string|number} record.count - 
   * @param {string} record.type - 
   * @param {string} record.time - , timestamp
   * */
  _storeRecord (record) {
    let _len = this._history.length
    if (_len > 0) {
      let _lastRecord = this._history.pop()
      if (_lastRecord.type === record.type && _lastRecord.message === record.message) {
        _lastRecord.time = record.time
        _lastRecord.count++
        this._history.push(_lastRecord)
      } else {
        this._history.push(_lastRecord)
        this._history.push(record)
      }
    } else {
      this._history.push(record)
    }

    this._logPageInstance && this._logPageInstance.setRecordList(this._history)
  }
}

// Normalize Error objects across all browsers into something semi-standard.
// Not all properties are will be available, but if they are, they'll at
// least have a predictable property name.
function normalizeError (err) {
  var o = {
    message: err.message,
    script: err.fileName || err.sourceURL || err.script,
    name: err.name,
    line: err.lineNumber || err.line,
    stack: err.stackTrace || err.stack
  }

  // Chrome doesn't provide script/line # properties in Error objects, and
  // only reports the script/line # of where an error was last rethrown (as
  // opposed to the original throw point).  So we scrape the stack trace to
  // get that info
  if (window.chrome && err.stack &&
    /(\w{3,5}:\/\/[^:]+):(\d+)/.test(err.stack)) {
    o.script = RegExp.$1
    o.line = RegExp.$2
  }
  // Clear out undefined properties
  for (var k in o) {
    if (o[k] === null || o[k] === undefined) {
      delete o[k]
    }
  }
  return o
}



