/**
 * @component Modal
 * @description
 *
 * ##  / Modal
 *
 * ## 
 *
 * Model, , , H5 historypopstate, Modal, modal.
 *
 * ## 
 *
 * - modalmodal
 * - modalurl
 * - modal, `template`, `modalData`,
 * - `onDismiss`modal.
 * - Page, 
 *
 * ### 
 *
 * mode, , mode. , zoomfade.
 *
 * @usage
 * // 
 * openModal () {
 *        this.$modal.present({
 *          mode: 'zoom',
 *          component: modalPageComponent,
 *          data: {hello: 'Page1Data'},
 *          showBackdrop: true,
 *          enableBackdropDismiss: true,
 *          onDismiss (data) {
 *            console.debug('modal1')
 *            console.debug(JSON.stringify(data))
 *          }
 *    })
 * },
 *
 * // 
 * closeModal () {
 *        this.$modal.dismiss({
 *          result: 'modal 1 dismissed success!'
 *        })
 * }
 * @demo https://dtfe.github.io/vimo-demo/#/modal
 * */

import Vue from 'vue'
import { getInsertPosition } from '../../util/getInsertPosition'
import { registerListener } from '../../util/util'
import modalComponent from './modal.vue'
let modalArr = []
let unRegisterUrlChange = []
const Modal = Vue.extend(modalComponent)
let isModalEnable = true // modalfalse, true
// ---------- functions ----------

/**
 * 
 * @private
 */
function ModalFactory (options) {
  return new Modal({
    el: getInsertPosition('modalPortal').appendChild(document.createElement('div')),
    propsData: options
  })
}

/**
 * Modal
 * : ()/(modal)/(template), Modal, templateModal, urlChange. Modal,
 * modal_present.
 *
 * @param {String} [mode]
 * @param {VueComponent} component - modal
 * @param {object} [data] - modal
 * @param {function} [onDismiss] - model, data
 * @param {Boolean} [showBackdrop=true] - backdrop
 * @param {Boolean} [enableBackdropDismiss=true] - backdrop
 *
 *
 * @param {string} [options.mode='ios'] - 
 * @example
 * :
 * {
 *  component:require('*.vue'),  // modal
 *  data:{...},            // modal
 *  onDismiss(data){....},      // model, data
 * }
 *
 *  this.$options.$data.username 
 * */
function present (options = {}) {
  isModalEnable = false

  let modalInstance = ModalFactory(options)
  let presentPromise = modalInstance.present()

  // 
  window.history.pushState({
    id: new Date().getTime()
  }, '', '')

  // record
  modalArr.push(modalInstance)

  // url
  if (unRegisterUrlChange.length === 0) {
    registerListener(window, 'popstate', function () {
      if (isModalEnable) {
        // modal
        let _lastModal = modalArr.pop()
        _lastModal && _lastModal.dismiss()
        // urlChange
        if (modalArr.length === 0) {
          unregisterAllListener()
        }
      }
    }, {}, unRegisterUrlChange)
  }

  // window.setTimeout(() => { isModalEnable = 0 }, 400)
  presentPromise.then(() => {
    isModalEnable = true
  })

  return presentPromise
}

/**
 * dismiss
 * dismissModal, onDismiss, , , modal, modal, .
 * `dataBack``onDismiss`
 * @param {any} dataBack -  modaldismiss
 * */
function dismiss (dataBack) {
  isModalEnable = false

  return new Promise((resolve) => {

    // modal
    let lastModalInstance = modalArr.pop()

    // urlChange
    if (modalArr.length === 0) {
      unregisterAllListener()
    }

    window.history.back()
    lastModalInstance.dismiss(dataBack).then(() => {
      isModalEnable = true
      // // onDismiss
      resolve()
    })
  })
}

// 
function unregisterAllListener () {
  unRegisterUrlChange.forEach((item) => item && item())
  unRegisterUrlChange = []
}

export default {
  present,
  dismiss
}



