<template>
    <article class="ion-app" :version="version"
             :class="[modeClass,platformClass,hoverClass,{'disable-scroll':isScrollDisabled}]">
        <!--app-root start-->
        <section class="app-root">
            <slot></slot>
        </section>
        <!--modal portal-->
        <aside id="modalPortal"></aside>
        <!--,action-sheet,choose-sheet,picker sheetPortal-->
        <aside id="sheetPortal"></aside>
        <!--alert portal-->
        <aside id="alertPortal"></aside>
        <!--loading portal-->
        <aside id="loadingPortal"></aside>
        <!--toast portal-->
        <aside id="toastPortal"></aside>
        <!--bugz-index=99999-->
        <aside class="click-block"
               :class="[{'click-block-enabled':isClickBlockEnabled}]"></aside>
    </article>
</template>
<style lang="scss">
    @import './app.scss';
    @import './app.ios.scss';
    @import './app.md.scss';
    @import './ios-transition.scss';
    @import './fade-bottom-transition.scss';
    @import './fade-right-transition.scss';
    @import './zoom-transition.scss';
    @import './fade-transition.scss';

    $color: color($colors, primary);
    $dots-svg: "<svg t='1497703040194' viewBox='0 0 1026 1024' version='1.1' xmlns='http://www.w3.org/2000/svg' p-id='2519' xmlns:xlink='http://www.w3.org/1999/xlink' width='24' height='24'><path d='M110.98112 510.74048m-110.98112 0a21.676 21.676 0 1 0 221.96224 0 21.676 21.676 0 1 0-221.96224 0Z' p-id='2520' fill='fg-color'></path><path d='M512.93184 510.74048m-110.98112 0a21.676 21.676 0 1 0 221.96224 0 21.676 21.676 0 1 0-221.96224 0Z' p-id='2521' fill='fg-color'></path><path d='M914.87744 510.74048m-110.98112 0a21.676 21.676 0 1 0 221.96224 0 21.676 21.676 0 1 0-221.96224 0Z' p-id='2522' fill='fg-color'></path></svg>";
    @mixin options-icon($svg-icon, $fg-color) {
        $svg: str-replace($svg-icon, 'fg-color', $fg-color);
        @include svg-background-image($svg);
    }

    .icon-dots {
        height: 30px;
        width: 30px;
        @include options-icon($dots-svg, $color);
        background-repeat: no-repeat;
        background-position: center center;
    }


</style>
<script type="text/javascript">
  /**
   * @component Base/App
   * @description
   *
   *  ##  / App
   *
   * AppVimo,.
   * App`vue.prototype.$app`, , :
   *
   * ```
   * this.$app.setTitle('Hello World');
   * ```
   *
   * , , id:
   *
   * - modalPortal
   * - sheetPortal
   * - alertPortal
   * - loadingPortal
   * - toastPortal
   *
   * ### 
   *
   * , , , Vimo, . .
   *
   * : **App/Nav/Page/Header/Footer/Content**, 6.
   *
   * ### 
   *
   * -Text Alignment
   *    - [text-left]          - 
   *    - [text-center]        - 
   *    - [text-right]         - 
   *    - [text-justify]       - 
   *    - [text-nowrap]        - 
   *
   *
   * -Text Transformation
   *    - [text-uppercase]     - 
   *    - [text-lowercase]     - 
   *    - [text-capitalize]    - 
   *
   * -Normal
   *    - [padding]         - padding, 16px
   *    - [no-padding]      - padding
   *    - [hidden]      - display:none
   *    - .hidden       - display:none
   *
   * @props {String} [mode='ios'] - 
   *
   * @demo https://dtfe.github.io/vimo-demo/#/app
   * */

  import { ClickBlock } from './click-block'
  import { setElementClass } from '../../util/util'
  const CLICK_BLOCK_BUFFER_IN_MILLIS = 64       // click_blcok
  const CLICK_BLOCK_DURATION_IN_MILLIS = 700    // 
  const ACTIVE_SCROLLING_TIME = 100
  const clickBlockInstance = new ClickBlock()

  let scrollDisTimer = null                     // 
  export default{
    name: 'App',
    data () {
      return {
        disabledTimeRecord: 0,        // 
        scrollTimeRecord: 0,        // 
        isScrollDisabled: false, // 
        isClickBlockEnabled: false, //  '' click-block-enabled

        version: window.VM.version
      }
    },
    props: {
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      }
    },
    computed: {
      modeClass () {
        return `${this.mode}`
      },
      platformClass () {
        return `platform-${this.mode}`
      },
      hoverClass () {
        let _isMobile = navigator.userAgent.match(/AppleWebKit.*Mobile.*/)
        return _isMobile ? 'disable-hover' : 'enable-hover'
      }
    },
    methods: {
      // -------- public --------

      /**
       * @function setEnabled
       * @description
       * , ActionSheet/Alert/Modaltransition,
       * transitionApp, , 
       * @param {boolean} isEnabled  - `true` for enabled, `false` for disabled
       * @param {number} duration - isEnabled=false  `isEnabled` `false`, duration`isEnabled``true`
       *
       * @example
       * this.$app && this.$app.setEnabled(false, 400) -> 400ms, 400ms
       * this.$app && this.$app.setEnabled(true) -> 64ms
       * */
      setEnabled (isEnabled, duration = CLICK_BLOCK_DURATION_IN_MILLIS) {
        this.disabledTimeRecord = (isEnabled ? 0 : Date.now() + duration)
        if (isEnabled) {
          // disable the click block if it's enabled, or the duration is tiny
          clickBlockInstance.activate(false, CLICK_BLOCK_BUFFER_IN_MILLIS)
        } else {
          // show the click block for duration + some number
          clickBlockInstance.activate(true, duration + CLICK_BLOCK_BUFFER_IN_MILLIS)
        }
      },

      /**
       * @function isEnabled
       * @description
       * App()
       * @return {boolean}
       */
      isEnabled () {
        if (this.disabledTimeRecord === 0) return true
        return (this.disabledTimeRecord < Date.now())
      },

      /**
       * @function setDisableScroll
       * @description
       * , 
       * @param {Boolean} isScrollDisabled -  true:/false:
       * @param {number} duration - 
       * @example
       * this.$app && this.$app.setDisableScroll(true, 400) -> 400ms, 400ms
       * this.$app && this.$app.setDisableScroll(false) ->
       * */
      setDisableScroll (isScrollDisabled, duration = 0) {
        if (duration > 0 && isScrollDisabled) {
          this.isScrollDisabled = isScrollDisabled
          window.clearTimeout(scrollDisTimer)
          scrollDisTimer = window.setTimeout(() => {
            this.isScrollDisabled = false
          }, duration)
        }
      },
      /**
       * @function isScrolling
       * @description
       * appscroll, scroll, isScrollscroll
       * @return {boolean}
       */
      isScrolling () {
        if (this.scrollTimeRecord === 0) return false
        if (this.scrollTimeRecord < Date.now()) {
          this.scrollTimeRecord = 0
          return false
        }
        return true
      },

      /**
       * @function setScrolling
       * @description
       * App, : scroll-view, conent
       * ... ACTIVE_SCROLLING_TIME, , 
       * isScrolling
       */
      setScrolling () {
        this.scrollTimeRecord = Date.now() + ACTIVE_SCROLLING_TIME
      },

      /**
       * @function setClass
       * @description
       * appclass, 
       * @param {string} className
       * @param {boolean} isAdd
       */
      setClass (className, isAdd) {
        setElementClass(this.$el, className, isAdd)
      },

      /**
       * @function setDocTitle
       * @description
       * document.title
       * */
      setDocTitle (val) {
        // 
        if (this.$platform.platforms().length <= 2) {
          document.title = val
        } else {
          // jq
          let _docTitle = document.title
          if (val !== _docTitle) {
            // iframeonload
            document.title = val
            let iframe = document.createElement('iframe')
            // 
            iframe.src = 'data:image/gif;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQImWNgYGBgAAAABQABh6FO1AAAAABJRU5ErkJggg=='
            iframe.style.visibility = 'hidden'
            iframe.style.width = '1px'
            iframe.style.height = '1px'
            iframe.onload = function () {
              window.setTimeout(function () {
                document.body.removeChild(iframe)
              }, 0)
            }
            document.body.appendChild(iframe)
          }
        }
      }
    },
    created () {
      /**
       * $app
       * */
      let proto = Reflect.getPrototypeOf(Reflect.getPrototypeOf(this))
      proto.$app = this
    },
    mounted () {
      // 
      this.isClickBlockEnabled = true
    }
  }
</script>



