<template>
    <div class="ion-buttons bar-buttons"
         :class="[modeClass]"
         :left="left"
         :right="right"
         :end="end">
        <slot></slot>
    </div>
</template>
<style lang="scss"></style>
<script type="text/javascript">
  /**
   * @component Buttons
   * @description
   *
   * ## (Toolbar) / Buttons
   *
   * Toolbar. start/left/right/end. Button
   *
   * ### 
   *
   * ```
   * // 
   * import { Toolbar, Buttons, Title } from 'vimo/components/toolbar'
   * // 
   * Vue.component(Toolbar.name, Toolbar)
   * Vue.component(Title.name, Title)
   * Vue.component(Buttons.name, Buttons)
   * ```
   *
   * ### 
   *
   * [left]/[right]/[end]
   *
   *
   *
   * @props {String} [mode=ios] - 
   * @props {Boolean} [left=false] - 
   * @props {Boolean} [right=false] - 
   * @props {Boolean} [end=false] - 
   *
   * @see component:Toolbar
   *
   * */
  export default{
    name: 'Buttons',
    props: {
      left: Boolean,
      right: Boolean,
      end: Boolean,
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      }
    },
    computed: {
      // 
      modeClass () {
        return `bar-buttons-${this.mode}`
      }
    },
    methods: {
      /**
       * @private
       * */
      getPosition () {
        if (this.right || this.end) {
          return 'right'
        }
        return 'left'
      }
    }
  }
</script>



