<template>
    <i class="ion-icon"
       :class="[colorClass,nameClass,itemClass,{'hide':hidden}]"></i>
</template>
<style lang="scss">
    @import "icon.scss";
</style>
<script type="text/javascript">
  /**
   * @component Icon
   * @description
   *
   * ##  / Icon
   *
   *
   * ### IONIC
   *
   * VimoIonic, Iconionicons, ionicons[](http://ionicons.com/), ioniconiosAndroid.
   *
   * ,ionicon, icon, font-awesome.
   *
   * ### 
   *
   * . , iconionicons/imgClass:
   *
   * ##### 1 .ionicons
   *
   *   nameioniconsname(ion/mode)
   *
   * ```
   *  <Icon name="star"></Icon>                     --  , ios:ion-ios-star, md:ion-md-star
   *  <Icon name="ios-star"></Icon>                 --  ion-ios-star
   *  <Icon name="star"></Icon>                     --  mode  ->  ion-ios-star/ion-android-star
   *  <Icon ios="ios-home" md="md-home"></Icon>     --  : iosios-home, mdmd-home
   * ```
   *
   * ##### 2. icon
   *
   *    , ionicons.
   *
   * ```
   *  <Icon name="icon-star"></Icon>                --  icon-star
   * ```
   *
   * ##### 3.  activeName
   * ```
   *  <Icon name="home" activeName="star"></Icon>  --  class
   * ```
   *
   * ### Class
   * [small] - icon
   *
   * ### 
   * ```
   * // 
   * import { Icon } from 'vimo/components/icon'
   * // 
   * Vue.component(Icon.name, Icon)
   * // 
   * export default{
   *   components: {
   *    Icon
   *  }
   * }
   * ```
   *
   *
   *
   * @props {String} [mode='ios'] - 
   * @props {String} [color] - 
   * @props {String|Boolean} [name] - icon, false, 
   * @props {String} [activeName] - IconIcon(ios, name: `${this.name}-outline`, )
   * @props {Boolean} [isActive=true] - , 
   * @props {String} [ios] - iosIcon
   * @props {String} [md] - mdIcon
   *
   *
   * @demo https://dtfe.github.io/vimo-demo/#/icon
   *
   * */
  export default{
    name: 'Icon',
    props: {
      /**
       * 
       * */
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('iconMode') || 'ios' }
      },
      /**
       * color
       * primary:    #007aff,
       * secondary:  #32db64,
       * danger:     #d91e18,
       * light:      #f4f4f4,
       * dark:       #222,
       * */
      color: [String],
      /**
       * icon, 
       * */
      name: [String, Boolean],

      /**
       * ,
       * ,:
       * ion -> `${this.name}-outline`
       * */
      activeName: [String],

      /**
       * 
       *
       * :
       * activeIcon, 
       *
       * namenormal
       * activeName
       *
       * */
      isActive: {
        type: Boolean,
        default: true
      },

      ios: [String],
      md: [String]
    },
    data () {
      return {
        nameClass: '', // nameClass
        itemClass: '',
        nameValue: null,
        hidden: false // 
      }
    },
    watch: {
      isActive () {
        this.update()
      },
      name () {
        this.update()
      },
      activeName () {
        this.update()
      },
      ios () {
        this.update()
      },
      md () {
        this.update()
      }
    },
    computed: {
      // 
      colorClass () {
        return this.color ? (`icon-${this.color}`) : ''
      }
    },
    methods: {
      getNameValue (val) {
        if (!(/^md-|^ios-|^logo-|^icon-/.test(val))) {
          // this does not have one of the defaults
          // so lets auto add in the mode prefix for them
          return this.mode + '-' + val
        } else {
          return val
        }
      },
      update () {
        let iconName
        this.hidden = !this.name && !this.ios && !this.md
        if (this.hidden) return

        if (this.isActive && this.activeName) {
          this.nameValue = this.getNameValue(this.activeName)
        } else {
          this.nameValue = this.getNameValue(this.name)
        }

        if (this.ios && this.mode === 'ios') {
          // ios-star ios
          iconName = this.ios
        } else if (this.md && this.mode === 'md') {
          // md-star md
          iconName = this.md
        } else {
          // ios-star md-star icon-star
          iconName = this.nameValue
        }

        let iconMode = iconName.split('-', 2)[0]
        if (iconMode === 'ios' && !this.isActive &&
          iconName.indexOf('logo-') < 0 &&
          iconName.indexOf('-outline') < 0) {
          // ios-star -> ios-star-outline
          iconName += '-outline'
        }
        // ios-star-outline -> ion-ios-star-outline
        // ios-star -> ion-ios-star-outline
        // icon-star -> icon-star
        if (iconMode === 'icon') {
          this.nameClass = iconName
        } else {
          this.nameClass = 'ion-' + iconName
        }
      }
    },
    created () {
      this.update()
    },
    mounted () {
      if (this.$parent.$el && this.$parent.$el.className && this.$parent.$el.className.indexOf('item') > -1) {
        // button in items should add class of 'item-button'
        this.itemClass = 'item-icon'
      }
    }
  }
</script>



