/**
 * @component Menus
 * @description
 *
 * ##  / Menus
 *
 * ### 
 *
 * menuApp.vuemenunav Vue.prototype.$menuthis.$menumenu
 *
 * ### 
 *
 * Menus$$eventBus, 
 *
 * ### 
 * ```
 * // 
 * import { Menus } from 'vimo/components/menus'
 * // 
 * Vue.component(Menus.name, Menus)
 * // 
 * export default{
   *   components: {
   *    Menus
   *  }
   * }
 * ```
 *
 * @props  {string}  id               - menuidopenid
 * @props  {String}  [side=left]      -    left/right
 * @props  {String}  [type=overlay]   -    overlay/reveal/push
 * @props  {boolean} [enabled=true]   - menu
 *
 * @fires component:Menus#onMenuOpen
 * @fires component:Menus#onMenuClosing
 * @fires component:Menus#onMenuClosed
 *
 * @demo https://dtfe.github.io/vimo-demo/#/
 *
 * @usage
 *
 * <Menu id="menu" side="left" type="push" :enabled="false"></Menu>
 *
 * var vm = new Vue();
 * vm.$menus.menuIns: menu
 * vm.$menus.currentMenuId: menuId
 * vm.$menus.open('menuId'): idmenuId1menu
 * vm.$menus.close(): menu
 * vm.$menus.toggle('menuId'): , idmenuId1menu
 *
 *
 *
 *
 */

// 

/**
 * @event component:Menus#onMenuOpen
 * @description menu, menuId,$eventBus.$on()
 * @example
 * new Vue({
   *    methods: {
   *     open: function () {
   *       this.$menu.open('aaa');
   *       this.$$eventBus.$on("onMenuOpen", function () {
   *         //...
   *       })
   *     }
   *   }
   * })
 *
 */

/**
 * @event component:Menus#onMenuClosing
 * @description menu,...,$eventBus.$on()
 */

/**
 * @event component:Menus#onMenuClosed
 * @description menu,$eventBus.#on()
 */

/**
 * menu.vue: Menu, 
 * menu.js:  , Vue.prototype.$menu
 *
 *
 * :
 * this.$menu.menuIns: menu
 * this.$menu.currentMenuId: menuId
 * this.$menu.open('menuId1'): idmenuId1menu
 * this.$menu.close(): menu
 * this.$menu.toggle('menuId1'): ,  menuId1
 *
 * menu, props
 *
 * :
 * onMenuOpen: menu, menuId
 * onMenuClosing: menu,...
 * onMenuClosed: menu
 *
 * */

// function
/**
 * @function open
 * @description
 * menumenu, promise
 * @param {String} menuId   - menuidid
 * @return {Promise}
 * @example
 * idaaamenu
 *
 *
 * html:
 *   <Menu id="aa"></Menu>
 *   <Menu id="bb"></Menu>
 * js:
 * new Vue({
    *    methods: {
    *     open: function () {
    *       this.$menu.open('aaa');
    *     }
    *   }
    * })
 */
/**
 * @function close
 * @return {Promise}
 * @example
 *
 * new Vue({
    *    methods: {
    *     open: function () {
    *       this.$menu.close();
    *     }
    *   }
    * })
 */
/**
 * @function toggle
 * @param {String} menuId   - menuid
 * @example
 *  idaaamenu
 *
 * html:
 *   <Menu id="aa"></Menu>
 *   <Menu id="bb"></Menu>
 * js:
 * new Vue({
    *    methods: {
    *     open: function () {
    *       this.$menu.toggle('aaa');
    *     }
    *   }
    * })
 */

import { urlChange } from '../../util/util'
export function recordMenuInstance (instance) {
  // 
  let proto = Reflect.getPrototypeOf(Reflect.getPrototypeOf(instance))
  if (!proto.$menus) {
    proto.$menus = new Menus()
  }
  proto.$menus.record(instance)
}

class Menus {
  constructor () {
    this.currentMenuId = null
    this.menuIns = {}
    this._unReg = null // for url change
  }

  /**
   * record
   * @param {object} instance
   * */
  record (instance) {
    this.menuIns[instance.id] = instance
  }

  /**
   * 
   * menumenu, promise
   *
   * @param {string} menuId - menuid
   * @return {promise}
   *
   * */
  open (id) {
    let _successCb
    let _errorCb
    if (this.currentMenuId) {
      this.close().then(() => {
        // debug: nextTick, 
        window.setTimeout(() => {
          _openMenu(this, id)
        }, 16 * 10)
      })
    } else {
      _openMenu(this, id)
    }

    function _openMenu (_this, id) {
      if (_this.menuIns[id]) {
        _this.currentMenuId = id
        _this.menuIns[id].openMenu()
        _successCb && _successCb()
      } else {
        _errorCb && _errorCb()
      }

      // for url change
      // urlmenu
      _this._unReg && _this._unReg()
      _this._unReg = urlChange(() => {
        _this.close()
      })
    }

    return new Promise((resolve, reject) => {
      _successCb = resolve
      _errorCb = reject
    })
  }

  /**
   * , 
   * @return {promise}
   * */
  close () {
    let currentMenuId = this.currentMenuId
    let _successCb
    let _errorCb

    if (!currentMenuId) {
      _errorCb && _errorCb()
    } else {
      this.currentMenuId = null
      if (this.menuIns[currentMenuId]) {
        this.menuIns[currentMenuId].closeMenu().then(() => {
          _successCb && _successCb()
        })
      } else {
        _errorCb && _errorCb()
      }
    }

    return new Promise((resolve, reject) => {
      // for url change
      this._unReg && this._unReg()
      _successCb = resolve
      _errorCb = reject
    })
  }

  /**
   * toggleid
   * @param {string} menuId - menuid
   * */
  toggle (id) {
    if (this.currentMenuId) {
      // open
      return this.close()
    } else {
      // close
      return this.open(id)
    }
  }
}



