/**
 * @component Toast
 * @description
 *
 * ##  / Toast
 *
 * Toast, . Toast//, , Toast, .
 *
 * ### 
 *
 * Toast, `this.$toast('Toast Bottom Only String')`, :
 * toast? , Toast:
 *
 * 1. duration 
 * 2. showCloseButton 
 * 3. , 
 * 4. `let toast = this.$toast('Toast Bottom Only String')`toast, `toast.dismiss()`
 *
 * Toast.
 *
 *
 * ### 
 *
 * - Toast`onDismiss`, Toast.
 * - `dismissOnPageChange`Toast, `false`.
 *
 * ### Toast
 *
 * #### 1. Message
 *
 *
 *```
 this.$toast('Bottom was added successfully')
 *```
 *
 * #### 2. MessageDuration
 *
 *```
 this.$toast('Bottom was added successfully',1000)
 *```
 *
 * #### 3. Options
 *
 *```
 this.$toast({
    message: 'Bottom was added successfully',
    duration: 3000,
    position: 'bottom',
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('2 onDismiss in promise success!')
    }
 })

 this.$toast({
    message: 'Top with Button was added successfully',
    duration: 3000, // 
    position: 'top',
    showCloseButton: true,
    closeButtonText: '',
    cssClass: 'showCloseBtnToastCssClass',
    dismissOnPageChange: true,
    onDismiss () {
      console.debug('5 onDismiss in promise success!')
    }
 })
 *```
 *
 * @props {string} message - Toastmessage, 
 * @props {number} [duration=3000] - Toast, 
 * @props {string} [position="bottom"] - Toast. : "top", "middle", "bottom".
 * @props {string} [cssClass] - , 
 * @props {boolean} [showCloseButton=false] - 
 * @props {string} [closeButtonText='Close'] - , Close
 * @props {boolean} [dismissOnPageChange=false] - , , 
 * @props {string} [mode='ios'] - 
 * @props {string} [onDismiss=noop] - 
 * @props {string} [type] - (/dingding) toast  none success / fail / exception / none exception 
 *
 * @demo https://dtfe.github.io/vimo-demo/#/toast
 */

import Vue from 'vue'
import { isObject, isString } from '../../util/util'
import toastComponent from './toast.vue'
const Toast = Vue.extend(toastComponent)
const DOM_INSERT_POSITION = 'toastPortal' // DOM
const DOM_INSERT_POSITION_FALLBACK = 'app' // fallback

/**
 * ToastInstance, 
 * @param {any} arguments - 
 * @private
 * */
function ToastFactory () {

  let _args = Array.from(arguments)
  let propsData = {}

  // get el position
  let _insertPosition = document.getElementById(DOM_INSERT_POSITION) || document.getElementById(DOM_INSERT_POSITION_FALLBACK) || document.body
  let el = _insertPosition.appendChild(document.createElement('div'))

  if (_args.length === 2) {
    propsData = {
      message: _args[0],
      duration: _args[1]
    }
  }

  if (_args.length === 1 && isString(_args[0])) {
    propsData = {
      message: _args[0]
    }
  }

  if (_args.length === 1 && isObject(_args[0])) {
    propsData = _args[0]
  }

  let isAlipayReady = window.VM.platform.is('alipay') && window.AlipayJSBridge && !propsData.isH5
  let isDingTalkReady = window.VM.platform.is('dingtalk') && window.dd && !propsData.isH5

  if (isAlipayReady) {
    console.info('Toast Alipay!')
    return {
      present () {
        window.ap.showToast({
          content: propsData.message || '',
          type: propsData.type || '', // toast  none success / fail / exception / none exception
                                      // 
          duration: propsData.duration || 2000
        }, function () {
          propsData.onDismiss && propsData.onDismiss()
        })
      },
      dismiss () {
        window.ap.hideToast()
      }
    }
  }

  if (isDingTalkReady) {
    console.info('Toast DingTalk!')
    return {
      present () {
        if (propsData.type === 'fail') { propsData.type = 'error' }
        window.dd.device.notification.toast({
          icon: propsData.type || '', // iconsuccesserror 0.0.2
          text: propsData.message || '', // 
          duration: propsData.duration / 1000 || 2, // [android(<=2s >2s)]
          delay: propsData.delay || 0, // 0
          onSuccess () {
            propsData.onDismiss && propsData.onDismiss()
          }
        })
      },
      dismiss () {}
    }
  }

  console.info('Toast H5!')
  return new Toast({
    el,
    propsData: propsData
  })
}

/**
 * Toast
 * @function present
 * @description Toast
 * @param {object} args - 
 * @param {string} args.message - The message for the toast. Long strings will wrap and the toast container will
 *   expand.
 * @param {number} [args.duration=3000] - How many milliseconds to wait before hiding the toast.
 * @param {string} [args.position="bottom"] - The position of the toast on the screen. Accepted values: "top",
 *   "middle", "bottom".
 * @param {string} [args.cssClass] - Additional classes for custom styles, separated by spaces.
 * @param {boolean} [args.showCloseButton=false] - Whether or not to show a button to close the toast.
 * @param {string} [args.closeButtonText='Close'] - Text to display in the close button.
 * @param {boolean} [args.dismissOnPageChange=false] - Whether to dismiss the toast when navigating to a new page or
 *   nav back.
 * @return {ToastInstance} Toast
 * @private
 * */
export default function (...args) {
  let _instance = ToastFactory(...args)
  // 
  _instance.present()
  return _instance
}



