/**
 * @class Config
 * @classdesc **App**
 *
 *
 * ### 
 *
 * ConfigApp, . , , .
 *
 * , . , this.$config, :
 *
 * ```
 * this.$config.get('mode') => 'ios/md'
 * ```
 *
 * configVimo, `window.VM.config`, .
 *
 * ### 
 *
 * url, urlApp,  http://xx.xx.com?vmMode=ios
 * modeios, modemode, isMatch, !!
 *
 * #### 
 *
 * ** url(URL) > (Config) > (Platform) **
 *
 * ```
 * (URL):
 * http://xx.xx.com?vmMode=ios
 *
 * (Config):
 * src/config/app-configs.jsplatforms
 * export default {
 *    domain: '',
 *    platforms: {
 *        ios: {
 *          mode: 'md'
 *        }
 *    }
 * }
 *
 * (Config):
 * src/config/app-configs.js
 * export default {
 *    domain: '',
 *    mode: 'ios'
 * }
 *
 * (Platform):
 * _platforms = ['mobile','ios','wechat']
 * src/config/platform-configs.js
 * export default {
 *    wechat: {
 *        settings: {
 *          mode: 'md'
 *        }
 *    }
 * }
 *
 * (Platform):
 * _platforms = ['mobile','ios','wechat']
 * src/config/platform-configs.js
 * export default {
 *    ios: {
 *        settings: {
 *          mode: 'md'
 *        }
 *    }
 * }
 * ```
 *
 * #### 
 *
 *  / params      |  / IOS      |  / Android   |  / Description
 * -----------------|----------------|-----------------|------------------
 * mode             | ios            | md              | 
 * hideNavBar       | false          | false           | navbar
 * toolbarMinHeight | 44             | 56              | toolbar
 * iconMode         | ios            | md              | icon
 * menuType         | reveal         | overlay         | menu
 * backButtonText   | ''             | ''              | 
 * backButtonIcon   | ''             | ''              | icon
 * spinner          | ios            | crescent        | 
 * tabsHighlight    | false          | true            | tab
 * tabsPlacement    | bottom         | bottom          | tab
 * pageTransition   | ios-transition | zoom-transition | 
 *
 *
 *
 **/

const isFunction = (val) => typeof val === 'function'
const isDefined = (val) => typeof val !== 'undefined'
const isObject = (val) => typeof val === 'object'
const isArray = Array.isArray
// urlApp, htttp://xx.xx.com?vmMode=ios
const URL_CONFIG_PREFIX = 'vm'

class Config {
  constructor () {
    /**
     * @private
     * */
    this._c = {} // cache , config!
    this._s = {} // setting superlative  config
    this.plt = null // Platform 
  }

  /**
   * , , :
   * ```
   * config.platforms.ios.key > config.key
   * ```
   * @param {any} config - config, ()
   * @param {Platform} [plt] - platform, 
   * @private
   */
  init (config, plt) {
    this._s = config && isObject(config) && !isArray(config) ? config : {}
    this.plt = plt
  }

  /**
   * 
   * @param {string} [key] - key
   * @param {any} [fallbackValue] - 
   * @return {String}
   */
  get (key, fallbackValue = null) {
    const platform = this.plt

    // 
    if (!isDefined(this._c[key])) {
      if (!isDefined(key)) {
        // eslint-disable-next-line no-throw-literal
        throw 'config key is not defined'
      }

      // , 
      // , , :
      // url_params > config > plt._register[platform]
      let userPlatformValue //  config[platforms][platform][key]
      let userDefaultValue = this._s[key] // config[key]
      let platformValue // this.plt._registry[platformName].setting[key]
      let configObj = null

      if (platform) {
        // queryParam, _c, key
        var queryStringValue = platform.getQueryParam(URL_CONFIG_PREFIX + key)
        if (isDefined(queryStringValue)) {
          if (queryStringValue === 'true') {
            this._c[key] = true
          } else if (queryStringValue === 'false') {
            this._c[key] = false
          } else {
            this._c[key] = queryStringValue
          }
          return this._c[key]
        }

        // platform, , , : ['mobile','ios','wechat']
        var activePlatformKeys = platform.platforms()

        // , config.platforms
        for (var i = 0, ilen = activePlatformKeys.length; i < ilen; i++) {
          if (this._s.platforms) {
            configObj = this._s.platforms[activePlatformKeys[i]]
            if (configObj) {
              if (isDefined(configObj[key])) {
                userPlatformValue = configObj[key]
              }
            }
          }

          // (platform-registry.js)
          // [mode].setting
          configObj = platform.getPlatformConfig(activePlatformKeys[i])
          if (configObj && configObj.settings) {
            if (isDefined(configObj.settings[key])) {
              // found a setting for this platform
              platformValue = configObj.settings[key]
            }
          }
        }
      }

      // 
      // : config.platforms >  config   > platform
      // eg: config[platforms][platformName][key] > config[key]  > this.plt._registry[platformName].setting[key]
      this._c[key] = isDefined(userPlatformValue) ? userPlatformValue : isDefined(userDefaultValue) ? userDefaultValue : isDefined(platformValue) ? platformValue : null
    }

    var rtnVal = this._c[key]
    // platform
    if (isFunction(rtnVal)) {
      rtnVal = rtnVal(platform)
    }

    return (rtnVal !== null ? rtnVal : fallbackValue)
  }

  /**
   * get(), boolean, "true"true
   * @param {string} [key] - key
   * @param {boolean} [fallbackValue] - 
   * value was `null`. Fallback value defaults to `false`.
   * @return {Boolean}
   */
  getBoolean (key, fallbackValue = false) {
    const val = this.get(key)
    if (val === null) {
      return fallbackValue
    }
    if (typeof val === 'string') {
      return val === 'true'
    }
    return !!val
  }

  /**
   * get(), number
   * @param {string} [key] - key
   * @param {number} [fallbackValue] - 
   * value turned out to be `NaN`. Fallback value defaults to `NaN`.
   * @return {Number}
   */
  getNumber (key, fallbackValue = NaN) {
    const val = parseFloat(this.get(key))
    return isNaN(val) ? fallbackValue : val
  }

  /**
   * config, . .
   * @param {string} [platform] - , (either 'ios' or 'android'). .
   * @param {string} [key] - key
   * @param {string} [value] - 
   *
   * @example
   * set('key', 'value') = set key/value pair
   * set('ios', 'key', 'value') = set key/value pair for platform
   *
   * @return {this}
   */
  set (...args) {
    const arg0 = args[0]
    const arg1 = args[1]

    switch (args.length) {
      case 2:
        // set('key', 'value') = set key/value pair
        // arg1 = value
        this._s[arg0] = arg1
        delete this._c[arg0] // clear cache
        break

      case 3:
        // set('ios', 'key', 'value') = set key/value pair for platform
        // arg0 = platform
        // arg1 = key
        // arg2 = value
        this._s.platforms = this._s.platforms || {}
        this._s.platforms[arg0] = this._s.platforms[arg0] || {}
        this._s.platforms[arg0][arg1] = args[2]
        delete this._c[arg1] // clear cache
        break
    }

    return this
  }

  /**
   *
   * 
   * 1. _s,
   * 2. `settings({...})`, _s, _c
   * 3. `settings('ios', {...})`, _s.platforms['ios'], _c
   *
   * @param {*} arg0 - 0
   * @param {*} arg1 - 1
   *
   * @example
   * settings()
   * settings({...})
   * settings('ios', {...})
   *
   */
  settings (arg0, arg1) {
    switch (arguments.length) {
      case 0:
        return this._s

      case 1:
        // settings({...})
        this._s = arg0
        this._c = {} // clear cache
        break

      case 2:
        // settings('ios', {...})
        this._s.platforms = this._s.platforms || {}
        this._s.platforms[arg0] = arg1
        this._c = {} // clear cache
        break
    }

    return this
  }
}

/**
 * @function setupConfig
 * @description Config
 * @private
 * */
export function setupConfig (config = {}, plt = {}) {
  if (!!window['VM'] && !!window['VM']['config']) {
    return window['VM']['config']
  } else {
    const c = new Config()
    c.init(config, plt)
    window['VM'] = window['VM'] || {}
    window['VM']['config'] = c
    return c
  }
}



