<template>
    <div class="grid">
        <slot></slot>
    </div>
</template>
<style lang="scss">
    @import "grid.scss";
</style>
<script type="text/javascript">
  /**
   * @component Grid
   * @description
   *
   * ##  / Grid
   *
   * ### 
   *
   *  **Bootstrap's grid system**, flex.
   *
   * Vimo**Grid/Row/Column**, Gridflex, Row, Column. 12. , Scss, .
   *
   * > : BootstrapFlexbox.
   *
   * ### Grid/Row/Colume
   *
   * , flex, :
   *
   * - Grid:
   *    - [no-padding]: padding
   *    - [sudoku]: 
   *
   * - Row:
   *    - [nowrap]
   *    - [wrap-reverse]
   *    - [align-items-start]
   *    - [align-items-center]
   *    - [align-items-end]
   *    - [align-items-stretch]
   *    - [align-items-baseline]
   *    - [justify-content-start]
   *    - [justify-content-center]
   *    - [justify-content-end]
   *    - [justify-content-around]
   *    - [justify-content-between]
   *
   * - Column:
   *    - [align-self-start]
   *    - [align-self-end]
   *    - [align-self-center]
   *    - [align-self-stretch]
   *    - [align-self-baseline]
   *
   *
   * ### Column
   *
   * - (breakpoint):
   *    - xs: 0
   *    - sm: 576px
   *    - md: 768px
   *    - lg: 992px
   *    - xl: 1200px
   *
   * - :
   *    - [col-${breakpoint}-${i}]
   *    - [offset-${breakpoint}-${i}]: margin-left
   *    - [pull-${breakpoint}-${i}]: right
   *    - [push-${breakpoint}-${i}]: left
   *
   * ### 
   *
   * ```
   * // 
   * import { Column, Grid, Row } from 'vimo/components/grid'
   * // 
   * Vue.component(Grid.name, Grid)
   * Vue.component(Row.name, Row)
   * Vue.component(Column.name, Column)
   * ```
   *
   * @demo https://dtfe.github.io/vimo-demo/#/grid
   * @usage
   * <Grid no-padding>
   *    <Row justify-content-center align-items-center>
   *        <Column pull-sm-4>
   *            1 of 3
   *        </Column>
   *        <Column>
   *            2 of 3
   *        </Column>
   *        <Column>
   *            3 of 3
   *        </Column>
   *    </Row>
   * </Grid>
   * */
  export default{
    name: 'Grid'
  }
</script>



